import React, { JSX, PropsWithChildren, useMemo } from 'react';
import css from './CardPrevie.module.scss';
import cn from 'classnames';
import { ICardPreview } from '5_entities/cards/types/types';
import { useSortable } from '@dnd-kit/sortable';
import { ServerImg } from '6_shared/ui/ServerImg/ServerImg';

import { ReactComponent as SvgIconClip } from '6_shared/assets/icons/clip.svg'
import { ReactComponent as SvgIconComments } from '6_shared/assets/icons/comments.svg'
import { useGetMeQuery } from '5_entities/profile';

interface CardPrevieProps {
    cardData: ICardPreview;
}

export function CardPrevie({ children, cardData }: PropsWithChildren<CardPrevieProps>): JSX.Element {
    const { data: me } = useGetMeQuery();
    const {
        attributes,
        listeners,
        setNodeRef,
        isDragging,
    } = useSortable({
        id: cardData.cardId,
        data: {
            type: 'Card',
            card: cardData,
        }
    });

    const isImages = !!cardData.images.length;
    const isComments = !!cardData.comments.length
    const isNewComments = useMemo(() => {
        if (!isComments) return false;
        let statusValue = false;

        commentSeries: for (let comment of cardData.comments) {
            for (let status of comment.readStatuses) {
                if (status.userId === me?.id && !status.isRead) {
                    statusValue = true;
                    break commentSeries;
                }
            }
        }

        return statusValue;

    }, [isComments, cardData, me]);



    return (
        <div
            ref={setNodeRef}
            {...listeners}
            {...attributes}
            className={cn(css.CardPrevie, { [css.dragging]: isDragging })}
        >
            {isImages
                && <div className={css.CardPrevie__cover}>
                    <ServerImg src={cardData.images[0].url} />
                </div>
            }
            <div className={css.CardPrevie__inner}>
                {children}
            </div>

            {(isImages || isComments)
                && <div className={css.CardPrevie__info}>
                    {isImages
                        && <div className={css.CardPrevie__info_item}>
                            <SvgIconClip /> <span>{cardData.images.length}</span>
                        </div>
                    }
                    {isComments
                        && <div className={css.CardPrevie__info_item}>
                            <SvgIconComments /> <span>{cardData.comments.length}</span>
                            {isNewComments && <span className={css.statusLabel}>NEW</span>}
                        </div>
                    }
                </div>
            }
        </div>
    );
}