import React, { JSX, useState } from 'react';
import css from './Members.module.scss';
import cn from 'classnames';
import { InviteUser } from '../InviteUser/InviteUser';
import { useNavigate } from 'react-router-dom';
import { useGetMembersQuery, useRemoveMemberMutation } from '../../api/membersApiSlice';

import { Avatar } from '5_entities/ui/Avatar/Avatar';
import { useGetMeQuery } from '5_entities/profile';
import { Button } from '6_shared/ui/Button/Button';
import { ReactComponent as SvgIconCLose } from '6_shared/assets/icons/close.svg';
import { ReactComponent as SvgIconRemove } from '6_shared/assets/icons/delete.svg';
import { PopOver } from '6_shared/ui/PopOver/PopOver';

interface MembersProps {
    boardId: number;
    isMembersShow: boolean;
    setMembersShow: (arg: boolean) => void;
    isGuest: boolean;
}

export function Members(
    { isGuest, boardId, isMembersShow, setMembersShow }: MembersProps
): JSX.Element {
    const { data: me } = useGetMeQuery();
    const {
        data: members,
        isLoading: isMembersLoading
    } = useGetMembersQuery({ meId: Number(me?.id), boardId });
    const [removeMember, { isLoading: isMemberRemoveLoading }] = useRemoveMemberMutation();
    const [isInviteUserShow, setIsInviteUserShow] = useState<boolean>(false);
    const navigate = useNavigate();

    return (
        <>
            {isMembersShow &&
                <div className={css.members}>
                    <button className={css.members__close} onClick={() => setMembersShow(false)}>
                        <SvgIconCLose />
                    </button>
                    <div className={css.members__head}>
                        <Button
                            onClick={() => setIsInviteUserShow(true)}
                            style={{ width: '100%' }}
                        >Invite new member</Button>
                    </div>
                    <div className="mt-2">
                        <ul className={css.members__list}>
                            {members && members.users.map(user =>
                                <li key={user.id} className={css.user}>
                                    <div className={css.user__photo}>
                                        <Avatar src={user.profilePhoto} />
                                    </div>
                                    <div className={css.user__name}>{user.login}</div>
                                    {isGuest
                                        ? me && me.id == user.id &&
                                        <div className={css.user__remove}>
                                            <PopOver
                                                mode='block'
                                                title={<h4>leaving a board member</h4>}
                                                content={
                                                    <>
                                                        <p>Are you sure you want to leave this member?</p>
                                                        <Button
                                                            disabled={isMemberRemoveLoading}
                                                            style={{ width: '100%' }}
                                                            variant='danger'
                                                            onClick={() => {
                                                                removeMember({ boardId, userId: user.id });
                                                                navigate('/boards');
                                                            }}
                                                        >Leave</Button>
                                                    </>
                                                }
                                            >
                                                <button
                                                    className={cn(css.user__remove_btn, css.text)}>
                                                    Leave Board
                                                </button>
                                            </PopOver>
                                        </div>
                                        : me && me.id !== user.id &&
                                        <div className={css.user__remove}>
                                            <PopOver
                                                mode='block'
                                                title={<h4>Removing a board member</h4>}
                                                content={
                                                    <>
                                                        <p>Are you sure you want to remove this member?</p>
                                                        <Button
                                                            disabled={isMemberRemoveLoading}
                                                            style={{ width: '100%' }}
                                                            variant='danger'
                                                            onClick={() => removeMember({ boardId, userId: user.id })}
                                                        >Remove</Button>
                                                    </>
                                                }
                                            >
                                                <button className={css.user__remove_btn}>
                                                    <SvgIconRemove />
                                                </button>
                                            </PopOver>
                                        </div>
                                    }

                                </li>
                            )}
                        </ul>
                    </div>
                </div>
            }
            <InviteUser boardId={boardId} isShow={isInviteUserShow} setShow={setIsInviteUserShow} />
        </>
    );
}