import { LoginFormData, RegisterFormData } from "../types/types";

export abstract class AuthApi {
    static baseUrl: string = `http://${window.currentHost}/api`;

    static async login(data: LoginFormData) {
        const response = await fetch(this.baseUrl + '/users/login', {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify(data)
        });
        return response;
    }

    static async me(jwt: string) {
        const response = await fetch(this.baseUrl + '/users/me', {
            headers: {
                'authorization': 'Bearer ' + jwt
            }
        });
        return response;
    }

    static async register(data: RegisterFormData) {
        const { confirm_password, ...rest } = data;
        const response = await fetch(this.baseUrl + '/users/register', {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify(rest),
        });
        return response;
    }
}