import React, { JSX, ReactElement, ReactNode } from 'react';
import css from './BoardsList.module.scss';
import cn from 'classnames';

import { IBoard } from '../../types/types';

import defaultItemBg from '../../assets/gradient-bg.svg';
import { NavLink } from 'react-router-dom';
import { ServerImg } from '6_shared/ui/ServerImg/ServerImg';

interface BoardsListProps {
    boards: IBoard[],
    lastItemSlot?: ReactElement | ReactNode;
    variant: 'grid' | 'list';
}

export function BoardsList({ boards, lastItemSlot, variant }: BoardsListProps): JSX.Element {
    return (
        <div className={cn(css.boardsList, css[variant])}>
            {boards.map(board =>
                <NavLink
                    key={board.id}
                    to={'/board/' + board.id}
                    className={({ isActive }) => cn(css.item, { [css.active]: isActive })}
                >
                    <div className={css.item__bg}>
                        {board.previewImage
                            ? <ServerImg src={board.previewImage} />
                            : <img src={defaultItemBg} alt="" />
                        }
                    </div>
                    <div className={css.item__name}>{board.name}</div>
                </NavLink>
            )}

            {lastItemSlot && <div className={css.item}>{lastItemSlot}</div>}
        </div>
    );
}