import React, { useLayoutEffect, useState } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import './App.scss';
import { privateRoutes, publicRoutes } from './routes/routes';

import { AuthApi, setAuth, setMe } from '4_features/auth';
import { IMe } from '4_features/auth/types/types';
import { Profile } from '4_features/profile';
import { useAppDispatch, useAppSelector } from '6_shared/store/hooks';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';
import { AppWebsocket } from '6_shared/websocket';

function App() {
    const [isLoading, setIsLoading] = useState<boolean>(true);
    const isAuth = useAppSelector(state => state.auth.isAuth);
    const dispatch = useAppDispatch();



    useLayoutEffect(() => {
        const auth = async () => {
            const jwt = localStorage.getItem('jwt');
            if (jwt) {
                const response = await AuthApi.me(jwt);
                if (response.ok) {
                    const me: { resultCode: number, data: IMe } = await response.json();
                    dispatch(setMe(me.data));
                    dispatch(setAuth(true));
                    AppWebsocket.connect(jwt);
                }
            }
            setIsLoading(false);
        }
        auth();
    }, [dispatch])

    if (isLoading) {
        return <SpinerOverlay size={1.5} />
    } else {
        return (
            <div className="App">
                <BrowserRouter>
                    {isAuth
                        ? <Routes>
                            {privateRoutes.map(route =>
                                <Route key={route.path} {...route} />
                            )}
                        </Routes>
                        : <Routes>
                            {publicRoutes.map(route =>
                                <Route key={route.path} {...route} />
                            )}
                        </Routes>
                    }
                </BrowserRouter>
                <Profile />
            </div>
        );
    }
}

export default App;