import { ICardPreview } from "5_entities/cards";
import { apiSlice } from "6_shared/api/apiSlice";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import {
    ColumnsWsTypesOfMessages,
    IColumnWithExtraId,
    ICreateColumnResponse,
    IDeleteColumnResponse,
    IGetColumnsResponse,
    IWsMessageColumnsCardData,
    IWsMessageColumnsData
} from "../types/types";
import { AppWebsocket } from "6_shared/websocket";

export const columnsApi = apiSlice.injectEndpoints({
    endpoints: (builder) => ({

        getColumns: builder.query({
            query: (boardId: number) => rquestWithJwtToken({ url: `/boards/${boardId}/columns` }),
            transformResponse: (responseData: IGetColumnsResponse) => {
                return responseData.data.map<IColumnWithExtraId>(col => ({ ...col, columnId: `column_${col.id}` }));
            },
            providesTags: ['Columns'],
            async onCacheEntryAdded(arg, { updateCachedData, cacheDataLoaded, cacheEntryRemoved, dispatch }) {
                const onColumnsWSAction = (_: WebSocket, data: IWsMessageColumnsData) => {
                    dispatch(
                        columnsApi
                            .util.upsertQueryData(
                                'getColumns',
                                arg,
                                data.map<IColumnWithExtraId>(col => ({ ...col, columnId: `column_${col.id}` }))
                            )
                    )
                }

                const onUpdateCard = (_: WebSocket, data: IWsMessageColumnsCardData) => {
                    updateCachedData(draft => {
                        draft.forEach(column => {
                            if (column.id === data.columnId) {
                                const index = column.cards.findIndex(c => c.cardId === data.cardId);
                                if (index < 0) return;
                                column.cards.splice(index, 1, data)
                            }
                        })
                    })
                }
                try {
                    await cacheDataLoaded;
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.createColumn, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.updateColumn, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.deleteColumn, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.createCard, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.updateCard, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.setNewColumn, onColumnsWSAction);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.deleteCard, onColumnsWSAction);

                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.uploadImages, onUpdateCard);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.deleteCardImage, onUpdateCard);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.createCardComment, onUpdateCard);
                    AppWebsocket.subscribe(ColumnsWsTypesOfMessages.deleteCardComment, onUpdateCard);
                } catch (error) {
                    console.log(error);
                }

                await cacheEntryRemoved;
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.createColumn, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.updateColumn, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.deleteColumn, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.createCard, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.updateCard, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.setNewColumn, onColumnsWSAction);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.deleteCard, onColumnsWSAction);

                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.uploadImages, onUpdateCard);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.deleteCardImage, onUpdateCard);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.createCardComment, onUpdateCard);
                AppWebsocket.unsubscribe(ColumnsWsTypesOfMessages.deleteCardComment, onUpdateCard);
            }
        }),

        createColumn: builder.mutation<ICreateColumnResponse, { boardId: number, name: string }>({
            query: ({ boardId, name }) => {
                return rquestWithJwtToken({
                    url: '/columns', method: 'POST', body: {
                        boardId,
                        name
                    }
                })
            },
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    const { data: createdColumnResponse } = await queryFulfilled;
                    const createdColumn: IColumnWithExtraId = {
                        ...createdColumnResponse.data,
                        columnId: `column_${createdColumnResponse.data.id}`
                    }
                    const patchResult = dispatch(
                        columnsApi.util.updateQueryData('getColumns', arg.boardId, (draftColumns) => {
                            draftColumns.push(createdColumn);
                        })
                    )
                } catch (error) {
                    console.log(error);
                }
            },
        }),

        updateColumn: builder.mutation({
            query: ({ columnId, name }: { boardId: number, columnId: number, name: string }) => {
                return rquestWithJwtToken({
                    url: `/columns/${columnId}`, method: 'PUT', body: {
                        name
                    }
                })
            },
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                const patchResult = dispatch(
                    columnsApi.util.updateQueryData('getColumns', arg.boardId, (draft) => {
                        draft.forEach(column => {
                            if (column.id === arg.columnId) {
                                column.name = arg.name
                            }
                            return column;
                        })
                    })
                );

                try {
                    await queryFulfilled;
                } catch (error) {
                    patchResult.undo();
                }
            }
        }),

        updateColumnOrder: builder.mutation({
            query: ({ columnId, order }: { boardId: number, columnId: number, order: number }) => {
                return rquestWithJwtToken({
                    url: `/columns/${columnId}`, method: 'PUT', body: {
                        order
                    }
                })
            }
        }),

        deleteColumn: builder.mutation<IDeleteColumnResponse, { boardId: number, columnId: number }>({
            query: ({ boardId, columnId }) => {
                return rquestWithJwtToken({
                    url: `/columns/${columnId}`, method: 'DELETE'
                })
            },
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    await queryFulfilled;

                    const patchResult = dispatch(
                        columnsApi.util.updateQueryData('getColumns', arg.boardId, (draftColumns) => {
                            const index = draftColumns.findIndex(col => col.id === arg.columnId);
                            draftColumns.splice(index, 1);
                        })
                    )
                } catch (error) {
                    console.log(error);
                }
            }
        })
    })
});

export const {
    useGetColumnsQuery,
    useCreateColumnMutation,
    useUpdateColumnMutation,
    useDeleteColumnMutation,
    useUpdateColumnOrderMutation,
} = columnsApi;

export function updateColumns(boardId: number, columns: IColumnWithExtraId[]) {
    return columnsApi.util.upsertQueryData('getColumns', boardId, columns);
}

export function addCardToColumn(boardId: number, columnId: number, cardData: ICardPreview) {
    return columnsApi.util.updateQueryData('getColumns', boardId, (draftColumns) => {
        draftColumns.forEach(col => {
            if (col.id === columnId) {
                col.cards.push(cardData);
            }
        })
    })
}

interface INewCardData {
    title?: string;
    description?: string;
}

export function updateCardInColumn(
    boardId: number,
    columnId: number,
    cardId: number,
    newData: INewCardData
) {
    return columnsApi.util.updateQueryData('getColumns', boardId, (draftColumns) => {
        draftColumns.forEach(col => {
            if (col.id === columnId) {
                col.cards.forEach(card => {
                    if (card.cardId === cardId) {
                        if (newData?.title) card.title = newData.title;
                        if (newData?.description) card.description = newData.description;
                    }
                })
            }
        })
    })
}

export function deleteCardFromColumn(
    boardId: number,
    columnId: number,
    cardId: number,
) {
    return columnsApi.util.updateQueryData('getColumns', boardId, (draftColumns) => {
        draftColumns.forEach(col => {
            if (col.id === columnId) {
                const index = col.cards.findIndex(card => card.cardId === cardId);
                col.cards.splice(index, 1);
            }
        })
    })
}

export function setNewCardOrder(
    boardId: number,
    columnId: number,
    activeId: number,
    overId: number
) {
    return columnsApi.util.updateQueryData('getColumns', boardId, (draftColumns) => {
        draftColumns.forEach(col => {
            if (col.id === columnId) {
                const oldIndex = col.cards.findIndex(card => card.cardId === activeId);
                const newIndex = col.cards.findIndex(card => card.cardId === overId);
                const cardData = col.cards.splice(oldIndex, 1);
                col.cards.splice(newIndex, 0, cardData[0])
            }
        })
    })
}

export function queryWithColumnsApi<T extends object>(arg: T, queryFunc: (...args: any[]) => void) {
    queryFunc({ ...arg, columnsApi })
}

export type ColumnsApi = typeof columnsApi;