export interface IBoard {
    id: number;
    name: string;
    createdById: number;
    previewImage: null | string;
    participants: IParticipants;
}

export interface IParticipants {
    id: number;
    boardId: 7;
    users: IParticipant[];
}

export interface IParticipant {
    id: number;
    email: string;
    login: string;
    profilePhoto: null | string;
}

export interface IGetOwnBoardsResponse {
    resultCode: number;
    data: IBoard[];
}

export interface ICreateBoardFormData {
    name: string;
    photo?: File[];
}

export interface ICreateBoardResponse {
    resultCode: number;
    data: IBoard;
}

export interface IGetBoardResponse extends ICreateBoardResponse {}

export interface IUpdateBoardResponse extends ICreateBoardResponse {}

export interface IUpdateBoardFormData extends ICreateBoardFormData {}

export enum BoardsWsTypesOfMessages {
    deleteBoard = 'ws/api/deleteBoard',
    updateBoardInfo = 'ws/api/updateBoardInfo',
    addParticipantToBoard = 'ws/api/addParticipantToBoard',
    removeParticipantFromBoard = 'ws/api/removeParticipantFromBoard'
}

export interface IWsMessageRemoveParticipantFromBoardData {
    removedUserId: number;
    participants: IParticipants;
}