import { AnyAction, ThunkDispatch } from "@reduxjs/toolkit";
import { PatchCollection } from "@reduxjs/toolkit/dist/query/core/buildThunks";

export interface ICardPreview {
    cardId: number;
    title: string;
    description: null | string;
    columnId: number;
    order: number;
    images: ICardImage[];
    comments: IComment[];
}

export interface ICreateCardResponse {
    resultCode: number;
    data: ICardPreview;
}

export interface IUpdateCardResponse extends ICreateCardResponse { }

export type CreateCardArgs = {
    boardId: number;
    columnId: number;
    title: string;
    onQueryFulfilled: (boardId: number, columnId: number, cardData: ICardPreview) => void;
};

export type UpdateCardArgs = {
    boardId: number;
    columnId: number;
    cardId: number;
    body: {
        title?: string;
        description?: string;
    }
    onBeforeQueryFulfilled: (dispatch: ThunkDispatch<any, any, AnyAction>) => PatchCollection
}

export type DeleteCardArgs = {
    cardId: number;
    onQueryFulfilled: (dispatch: ThunkDispatch<any, any, AnyAction>) => void;
};

export type SetNewOrderCardArgs = {
    cardId: number;
    order: number;
}

export type SetNewColumnCardArgs = {
    cardId: number;
    columnId: number;
    order: number;
}

export interface IGetCardResponse {
    resultCode: number;
    data: ICard;
}

export interface ICard {
    cardId: number;
    title: string;
    description: string | null;
    columnId: number;
    order: number;
    images: ICardImage[];
    comments: IComment[];
}

export interface ICardImage {
    id: number;
    url: string;
    imageName: string;
    cardId: number;
}

export interface IComment {
    id: number;
    text: string;
    userId: number;
    cardId: number;
    readStatuses: ICommentReadStatus[];
    user: IUser;
    createdAt: Date;
}

export interface ICommentReadStatus {
    id: number;
    userId: number;
    isRead: boolean;
    commentId: number;
}

interface IUser {
    id: number;
    login: string;
    email: string;
    profilePhoto: string | null;
}

export enum CardWsTypesOfMessages {
    uploadImages = 'ws/api/uploadImages',
    deleteCardImage = 'ws/api/deleteCardImage',
    createCardComment = 'ws/api/createCardComment',
    updateCardComment = 'ws/api/updateCardComment',
    deleteCardComment = 'ws/api/deleteCardComment'
}

export type IWsMessageCardData = ICard;