import { apiSlice } from "6_shared/api/apiSlice";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import { IGetAllUsersResponse } from "5_entities/users/types/types";

export const usersApi = apiSlice.injectEndpoints({
    endpoints: ( builder ) => ({
        getAllUsers: builder.query({
            query: (currentUserId: number) => rquestWithJwtToken({ url: '/users' }),
            transformResponse: (responseData: IGetAllUsersResponse, _, currentUserId) => 
                responseData.data.filter(user => user.id !== currentUserId),
            providesTags: ['Users'],
        }),
    })
});

export const { 
    useGetAllUsersQuery,
} = usersApi;