import React, { JSX } from 'react';
import { createPortal } from 'react-dom';
import css from './Profile.module.scss';
import cn from 'classnames';
import { selectIsPopupShow, setPopupShow } from '../store/profileSlice';

import { useForm } from 'react-hook-form';

import { Popup } from '5_entities/ui/Popup/Popup';
import { Avatar } from '5_entities/ui/Avatar/Avatar';
import { useGetMeQuery, useUpdateMeMutation } from '5_entities/profile';
import { IUpdateMe } from '5_entities/profile/types/types';
import { useAppDispatch, useAppSelector } from '6_shared/store/hooks';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';
import { Input } from '6_shared/ui/fields/Input';
import { Button } from '6_shared/ui/Button/Button';
import { useAppDropzone } from '6_shared/ui/Dropzone/useDropzone';

export function Profile(): JSX.Element {
    const isPopupShow = useAppSelector(selectIsPopupShow);
    const dispatch = useAppDispatch();
    const { data: me, isLoading } = useGetMeQuery();
    const [updateMe, { isLoading: isUpdating }] = useUpdateMeMutation();
    const { DropzoneContainer, resetDropzone } = useAppDropzone();

    const {
        register,
        handleSubmit,
        formState: { errors, isDirty },
        control,
        resetField
    } = useForm<IUpdateMe>({
        mode: 'onSubmit'
    });

    const onSubmit = async (data: IUpdateMe) => {
        try {
            await updateMe(data).unwrap();
            resetDropzone();
            resetField('photo');
        } catch (error) {
            console.log(error);
        }
    }

    return (
        createPortal(
            <>
                <Popup toggleShow={isPopupShow} onClose={() => dispatch(setPopupShow(false))}>
                    {isLoading || isUpdating && <SpinerOverlay zIndex={10} size={1.5} />}
                    <Popup.Header>
                        <h3>Profile</h3>
                    </Popup.Header>
                    <Popup.Body>
                        <div className={css.profile}>
                            <div className={cn(css.profile__head, css.head)}>
                                <div className={css.head__photo}>
                                    <Avatar
                                        src={me?.profilePhoto
                                            ? me?.profilePhoto + '?' + Date.now()
                                            : me?.profilePhoto
                                        }
                                    />
                                </div>
                                <div className={css.head__text}>
                                    <div className={css.head__login}>{me?.login}</div>
                                    <div className={css.head__email}>{me?.email}</div>
                                </div>
                            </div>
                            <div className={cn(css.profile__body,)}>
                                <form onSubmit={handleSubmit(onSubmit)}>
                                    <div>
                                        <h5>Change login</h5>
                                        <div className="mt-1">
                                            <Input
                                                type='text'
                                                {...register('login')}
                                                defaultValue={me?.login}
                                                errorState={errors.login ? true : false}
                                            />
                                        </div>
                                    </div>
                                    <div className="mt-3">
                                        <h5>{me?.profilePhoto ? 'Upload new photo' : 'Upload photo'}</h5>
                                        <div className="mt-1">
                                            <DropzoneContainer
                                                control={control}
                                                name={'photo'}
                                                multiple={false}
                                            />
                                        </div>
                                    </div>
                                    <div className="mt-3">
                                        <Button disabled={!isDirty}>Update Profile</Button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </Popup.Body>
                </Popup>
            </>,
            document.body
        )
    );
}