import React, { HtmlHTMLAttributes, JSX, ReactElement, ReactNode, MouseEvent } from 'react';
import css from './Popup.module.scss';
import cn from 'classnames';
import { Box } from '6_shared/ui/Box/Box';
import { ReactComponent as SvgIconCLose } from './close.svg';
import { CSSTransition } from 'react-transition-group';


interface PopupProps extends HtmlHTMLAttributes<ReactElement | ReactNode> {
    onClose: (e: MouseEvent<HTMLButtonElement | HTMLDivElement>) => void;
    toggleShow: boolean;
    componentDidMount?: () => void;
}

export function Popup({ children, toggleShow, onClose, componentDidMount }: PopupProps): JSX.Element {

    return (
        <CSSTransition
            in={toggleShow}
            timeout={300}
            mountOnEnter
            unmountOnExit
            classNames={{
                enter: css.enter,
                enterActive: css.enter_active,
                exit: css.exit,
                exitActive: css.exit_active
            }}
            onEnter={() => componentDidMount && componentDidMount()}
        >
            <div className={css.popup}>
                <div className={css.popup__inner} onClick={(e) => {
                    onClose(e);
                }}>
                    <div className={css.popup__container} onClick={(e) => e.stopPropagation()}>
                        <button className={css.popup__close} onClick={onClose}>
                            <SvgIconCLose />
                        </button>
                        <Box padding={4}>
                            <div className={css.popup__content}>
                                {children}
                            </div>
                        </Box>
                    </div>
                </div>
            </div>
        </CSSTransition>
    );
}


interface PopupChildrenProps extends HtmlHTMLAttributes<ReactElement | ReactNode> { }

Popup.Header = function ({ children }: PopupChildrenProps): JSX.Element {
    return (
        <div className={css.popup__header}>
            {children}
        </div>
    );
}
Popup.Body = function ({ children }: PopupChildrenProps): JSX.Element {
    return (
        <div className={css.popup__body}>
            {children}
        </div>
    );
}