import { IGetMembersResponse, IMembers } from "../types/types";

import { BoardsWsTypesOfMessages } from "5_entities/boards";
import { apiSlice } from "6_shared/api/apiSlice";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import { AppWebsocket } from "6_shared/websocket";

export const membersApi = apiSlice.injectEndpoints({
    endpoints: (builder) => ({
        getMembers: builder.query({
            query: ({ boardId }: { meId: number, boardId: number }) =>
                rquestWithJwtToken({ url: `/boards/${boardId}/participants` }),
            transformResponse: (responseData: IGetMembersResponse) => {
                return responseData.data;
            },
            providesTags: ['Members'],
            async onCacheEntryAdded(arg, { updateCachedData, cacheDataLoaded, cacheEntryRemoved }) {
                const onRemoveParticipantFromBoard = (
                    _: WebSocket,
                    data: { removedUserId: number, participants: IMembers }
                ) => {
                    if (arg.meId !== data.removedUserId) {
                        updateCachedData((draft) => {
                            draft.users = data.participants.users
                        })
                    }
                }
                const onAddParticipantToBoard = (_: WebSocket, data: { participants: IMembers }) => {
                    updateCachedData((draft) => {
                        draft.users = data.participants.users
                    })
                }
                try {
                    await cacheDataLoaded;
                    AppWebsocket.subscribe(
                        BoardsWsTypesOfMessages.removeParticipantFromBoard,
                        onRemoveParticipantFromBoard
                    );
                    AppWebsocket.subscribe(
                        BoardsWsTypesOfMessages.addParticipantToBoard,
                        onAddParticipantToBoard
                    );
                } catch (error) {
                    console.log(error);
                }

                await cacheEntryRemoved;
                AppWebsocket.unsubscribe(
                    BoardsWsTypesOfMessages.removeParticipantFromBoard,
                    onRemoveParticipantFromBoard
                );
                AppWebsocket.unsubscribe(
                    BoardsWsTypesOfMessages.addParticipantToBoard,
                    onAddParticipantToBoard
                );
            }
        }),
        addMember: builder.mutation({
            query: ({ boardId, userId }: { boardId: number, userId: number }) =>
                rquestWithJwtToken({
                    url: `/boards/${boardId}/participant/${userId}`,
                    method: 'POST'
                }),
            invalidatesTags: ['Members']
        }),
        removeMember: builder.mutation({
            query: ({ boardId, userId }: { boardId: number, userId: number }) =>
                rquestWithJwtToken({
                    url: `/boards/${boardId}/participant/${userId}`,
                    method: 'DELETE'
                }),
            invalidatesTags: ['Members', 'GuestBoards']
        })
    })
});

export const {
    useGetMembersQuery,
    useAddMemberMutation,
    useRemoveMemberMutation,
} = membersApi;