import React, { JSX, useState } from 'react';
import css from './CardDescription.module.scss';

import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit';
import { PatchCollection } from '@reduxjs/toolkit/dist/query/core/buildThunks';

import { updateCardInColumn } from '5_entities/columns';
import { useUpdateCardMutation } from '5_entities/cards';
import { TextareaAutoHeight } from '6_shared/ui/fields/TextareaAutoHeight';

interface CardDescriptionProps {
    boardId: number;
    columnId: number;
    cardId: number;
    cardDescription: string | null;
}

export function CardDescription(
    { boardId, columnId, cardId, cardDescription }: CardDescriptionProps
): JSX.Element {
    const descriptionText = cardDescription || '';
    const [editMode, setEditMode] = useState(false);
    const [textareaValue, setTextareaValue] = useState(descriptionText);
    const [updateCard] = useUpdateCardMutation();

    const onBeforeQueryFulfilled = (dispatch: ThunkDispatch<any, any, AnyAction>): PatchCollection => {
        return dispatch(
            updateCardInColumn(boardId, columnId, cardId, { description: textareaValue })
        )
    }

    const saveNewDescription = () => {
        setEditMode(false)
        if (textareaValue === descriptionText) {
            return;
        } else {
            updateCard({
                boardId,
                columnId: columnId,
                cardId: cardId,
                body: { description: textareaValue },
                onBeforeQueryFulfilled
            });
        }
    }

    return (
        <div className={css.CardDescription}>
            {!editMode
                ? <div
                    onClick={() => setEditMode(true)}
                    className={css.CardDescription__text}
                >
                    {!!descriptionText.length
                        ? descriptionText
                        : 'Add a more detailed description...'
                    }
                </div>
                : <TextareaAutoHeight
                    value={textareaValue}
                    setValue={setTextareaValue}
                    placeholder='Add a more detailed description...'
                    className={css.CardDescription__textarea}
                    onKeyDown={(e) => {
                        if (e.key === 'Enter' && !e.shiftKey) saveNewDescription()
                    }}
                    onBlur={() => saveNewDescription()}
                    rows={3}
                    autoFocus
                    onFocus={(e) => e.target.selectionStart = e.target.value.length}
                >
                    {descriptionText}
                </TextareaAutoHeight>
            }
        </div>
    );
}
