import { apiSlice } from "6_shared/api/apiSlice";
import { 
    IBoard, 
    ICreateBoardFormData, 
    ICreateBoardResponse, 
    IGetBoardResponse, 
    IGetOwnBoardsResponse, 
    IParticipants, 
    IUpdateBoardFormData, 
    IUpdateBoardResponse, 
    BoardsWsTypesOfMessages 
} from "../types/types";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import { AppWebsocket } from "6_shared/websocket";

export const boardsApi = apiSlice.injectEndpoints({
    endpoints: (builder) => ({
        getOwnBoards: builder.query({
            query: () => rquestWithJwtToken({ url: '/boards' }),
            transformResponse: (responseData: IGetOwnBoardsResponse) => {
                return responseData.data;
            },
            providesTags: ['OwnBoards']
        }),
        getGuestBoards: builder.query({
            query: ({ meId }: { meId: number }) => rquestWithJwtToken({ url: '/boards/guest' }),
            transformResponse: (responseData: IGetOwnBoardsResponse) => {
                return responseData.data;
            },
            providesTags: ['GuestBoards'],
            async onCacheEntryAdded(arg, { updateCachedData, cacheDataLoaded, cacheEntryRemoved }) {
                const onUpdateBoardInfo = (_: WebSocket, data: IBoard) => {
                    updateCachedData((draft) => {
                        const index = draft.findIndex(board => board.id === data.id);
                        draft.splice(index, 1, data);
                    })
                }
                const onDeleteBoard = (_: WebSocket, data: { boardId: number, message: string }) => {
                    updateCachedData((draft) => {
                        const index = draft.findIndex(board => board.id === data.boardId);
                        draft.splice(index, 1);
                    })
                }
                const onAddParticipantToBoard = (_: WebSocket, data: IBoard) => {
                    updateCachedData((draft) => {
                        const index = draft.findIndex(board => board.id === data.id);
                        if (index < 0) {
                            draft.push(data)
                        } else {
                            draft.splice(index, 1, data);
                        }
                    })
                }
                const onRemoveParticipantFromBoard = (_: WebSocket, data: { removedUserId: number, participants: IParticipants }) => {
                    if (arg.meId === data.removedUserId) {
                        console.log(data);
                        
                        updateCachedData((draft) => {
                            const index = draft.findIndex(board => board.id === data.participants.boardId);
                            if (index < 0) return;
                            draft.splice(index, 1);
                        })
                    }
                }
                try {
                    await cacheDataLoaded;
                    AppWebsocket.subscribe(BoardsWsTypesOfMessages.updateBoardInfo, onUpdateBoardInfo);
                    AppWebsocket.subscribe(BoardsWsTypesOfMessages.deleteBoard, onDeleteBoard);
                    AppWebsocket.subscribe(BoardsWsTypesOfMessages.addParticipantToBoard, onAddParticipantToBoard);
                    AppWebsocket.subscribe(BoardsWsTypesOfMessages.removeParticipantFromBoard, onRemoveParticipantFromBoard);
                } catch (error) {
                    console.log(error);
                }

                await cacheEntryRemoved;
                AppWebsocket.unsubscribe(BoardsWsTypesOfMessages.updateBoardInfo, onUpdateBoardInfo);
                AppWebsocket.unsubscribe(BoardsWsTypesOfMessages.deleteBoard, onDeleteBoard);
                AppWebsocket.unsubscribe(BoardsWsTypesOfMessages.addParticipantToBoard, onAddParticipantToBoard);
                AppWebsocket.unsubscribe(BoardsWsTypesOfMessages.removeParticipantFromBoard, onRemoveParticipantFromBoard);
            }
        }),
        createBoard: builder.mutation<ICreateBoardResponse, ICreateBoardFormData>({
            query: (body: ICreateBoardFormData) => {

                const formData = new FormData();
                formData.append('name', body.name);
                body.photo && formData.append('photo', body.photo[0]);

                return rquestWithJwtToken({ url: '/boards', method: 'POST', body: formData })
            },
            invalidatesTags: ['OwnBoards']
        }),
        getBoard: builder.query({
            query: (id: number) => rquestWithJwtToken({ url: '/boards/' + id }),
            transformResponse: (responseData: IGetBoardResponse) => {
                return responseData.data;
            },
            providesTags: (result, error, arg) => [{ type: 'Board', id: arg }],

            async onCacheEntryAdded(arg, { updateCachedData, cacheDataLoaded, cacheEntryRemoved }) {
                const onUpdateBoardInfo = (_: WebSocket, data: IBoard) => {
                    updateCachedData((draft) => {
                        draft.name = data.name;
                        draft.previewImage = data.previewImage;
                    })
                }

                try {
                    await cacheDataLoaded;
                    AppWebsocket.subscribe(BoardsWsTypesOfMessages.updateBoardInfo, onUpdateBoardInfo);
                } catch (error) {
                    console.log(error);
                }

                await cacheEntryRemoved;
                AppWebsocket.unsubscribe(BoardsWsTypesOfMessages.updateBoardInfo, onUpdateBoardInfo);
            }
        }),
        updateBoard: builder.mutation<IUpdateBoardResponse, { id: number, body: IUpdateBoardFormData }>({
            query: ({ id, body }: { id: number, body: IUpdateBoardFormData }) => {
                const formData = new FormData();
                formData.append('name', body.name);
                body.photo && formData.append('photo', body.photo[0]);

                return rquestWithJwtToken({ url: '/boards/' + id, method: 'PUT', body: formData })
            },
            invalidatesTags: (result, error, arg) => [{ type: 'Board', id: arg.id }, 'OwnBoards']
        }),
        deleteBoard: builder.mutation({
            query: (id: number) => rquestWithJwtToken({ url: '/boards/' + id, method: 'DELETE' }),
            invalidatesTags: ['OwnBoards']
        })
    })
});

export const {
    useGetOwnBoardsQuery,
    useGetGuestBoardsQuery,
    useCreateBoardMutation,
    useGetBoardQuery,
    useUpdateBoardMutation,
    useDeleteBoardMutation,
} = boardsApi;
