
import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "6_shared/store/types";

interface ProfileSlice {
    isPopupShow: boolean;
}

const initialState: ProfileSlice = {
    isPopupShow: false,
}

export const profileSlice = createSlice({
    name: 'profile',
    initialState,
    reducers: {
        setPopupShow(state, action: PayloadAction<boolean>) {
            state.isPopupShow = action.payload;
        },
    }
})

export const {
    setPopupShow,
} = profileSlice.actions;

export const selectIsPopupShow = (state: RootState) => state.profile.isPopupShow;

export const profileReducer = profileSlice.reducer;