
export abstract class AppWebsocket {
    private static ws: WebSocket;
    private static subscriptions = new Map<string, Array<((ws: WebSocket, ...arg: any[]) => void)>>();
    static _isConnect: boolean = false;

    static connect(jwt: string) {
        this.ws = new WebSocket(`ws://${window.currentHost}/api/ws?token=${jwt}`);

        this.ws.onopen = () => {
            this._isConnect = true;
        }
        this.ws.onmessage = async (event) => {
            const data: { type: string, body: object } = await JSON.parse(event.data);

            const subscriptionsArray = this.subscriptions.get(data.type);
            if(!subscriptionsArray) return;
            
            subscriptionsArray.forEach(fun => fun(this.ws, data.body));
        }

        this.ws.onerror = (error) => {
            console.log('ws error: ', error);
        }

        this.ws.onclose = () => {
            this._isConnect = false;
        }
    }

    static disconnect() {
        this.ws.close(1000, 'Client disconnect')
        this._isConnect = false;
    }

    static get isConnetc() {
        return this._isConnect
    }

    static subscribe<T>(type: string, callback: (ws: WebSocket, messageData: T) => void) {
        const subscriptionsArray = this.subscriptions.get(type);

        if(subscriptionsArray) {
            this.subscriptions.set(type, [...subscriptionsArray, callback]); 
        } else {
            this.subscriptions.set(type, [callback]); 
        }
    }

    static unsubscribe<T>(type: string, callback: (ws: WebSocket, messageData: T) => void) {
        const subscriptionsArray = this.subscriptions.get(type);
        if(!subscriptionsArray) return;
        const filteredArray = subscriptionsArray.filter(fun => fun !== callback);
        this.subscriptions.set(type, filteredArray);
    }
}