import { ICardPreview } from "5_entities/cards";
import { ICard } from "5_entities/cards/types/types";
import { date, number } from "yup";

export interface IColumn {
    id: number;
    name: string;
    boardId: number;
    order: number;
    cards: ICardPreview[];
}

export interface IColumnWithExtraId extends IColumn {
    columnId: string;
}

export interface IGetColumnsResponse {
    resultCode: number;
    data: IColumn[];
}

export interface ICreateColumnResponse {
    resultCode: number;
    data: IColumn;
}
export interface IDeleteColumnResponse {
    resultCode: number;
    data: {
        message: string;
    };
}

export enum ColumnsWsTypesOfMessages {
    createColumn = 'ws/api/createColumn',
    updateColumn = 'ws/api/updateColumn',
    deleteColumn = 'ws/api/deleteColumn',
    createCard = 'ws/api/createCard',
    updateCard = 'ws/api/updateCard',
    setNewColumn = 'ws/api/setNewColumn',
    deleteCard = 'ws/api/deleteCard',

    uploadImages = 'ws/api/uploadImages',
    deleteCardImage = 'ws/api/deleteCardImage',
    createCardComment = 'ws/api/createCardComment',
    deleteCardComment = 'ws/api/deleteCardComment'
}

export type IWsMessageColumnsData = IColumn[];
export type IWsMessageColumnsCardData = ICard;