import React, { JSX, useState } from 'react';
import css from './CardHead.module.scss';

import { PatchCollection } from '@reduxjs/toolkit/dist/query/core/buildThunks';

import { useUpdateCardMutation } from '5_entities/cards';
import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit';
import { updateCardInColumn } from '5_entities/columns';
import { TextareaAutoHeight } from '6_shared/ui/fields/TextareaAutoHeight';

interface cardHeadProps {
    boardId: number;
    columnId: number;
    columnTitle: string;
    cardId: number;
    cardTitle: string;
}

export function CardHead(
    { boardId, columnId, columnTitle, cardId, cardTitle }: cardHeadProps
): JSX.Element {
    const [editMode, setEditMode] = useState(false);
    const [textareaValue, setTextareaValue] = useState(cardTitle);
    const [updateCard] = useUpdateCardMutation();

    const onBeforeQueryFulfilled = (dispatch: ThunkDispatch<any, any, AnyAction>): PatchCollection => {
        return dispatch(
            updateCardInColumn(boardId, columnId, cardId, { title: textareaValue })
        )
    }

    const onBlurHandler = () => {
        setEditMode(false);
        if (textareaValue === cardTitle || textareaValue.trim().length === 0) {
            setTextareaValue(cardTitle);
        } else {
            updateCard({
                boardId,
                columnId: columnId,
                cardId: cardId,
                body: { title: textareaValue },
                onBeforeQueryFulfilled
            });
        }
    }

    return (
        <div className={css.CardHead}>
            <div className={css.CardHead__row} onClick={() => setEditMode(true)}>
                {!editMode
                    ? <h3 className={css.CardHead__title}>{cardTitle}</h3>
                    : <TextareaAutoHeight
                        autoFocus
                        className={css.CardHead__textarea}
                        value={textareaValue}
                        setValue={setTextareaValue}
                        onBlur={onBlurHandler}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter' && !e.shiftKey) onBlurHandler();
                        }}
                        onFocus={(e) => e.target.selectionStart = e.target.value.length}
                    >{cardTitle}</TextareaAutoHeight>
                }
            </div>
            <div className={css.CardHead__row}>
                In column: <strong>{columnTitle}</strong>
            </div>
        </div>
    );
}