import { apiSlice } from "6_shared/api/apiSlice";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import { IMeResponse, IUpdateMe } from "../types/types";
import { IUser } from "5_entities/users/types/types";

export const profileApi = apiSlice.injectEndpoints({
    endpoints: ( builder ) => ({
        getMe: builder.query<IUser, void>({
            query: () => rquestWithJwtToken({ url: '/users/me' }),
            transformResponse: (responseData: IMeResponse) => responseData.data,
            providesTags: ['Me'],
        }),
        updateMe: builder.mutation({
            query: (body: IUpdateMe) => {
                const formData = new FormData();
                formData.append('login', body.login);
                body.photo && formData.append('photo', body.photo[0]);
                return rquestWithJwtToken({ url: '/users/me', method: 'PUT', body: formData })
            },
            invalidatesTags: ['Me'],
        })
    })
});

export const { 
    useGetMeQuery,
    useUpdateMeMutation,
} = profileApi;