import React, { JSX, PropsWithChildren, useState } from 'react';
import { createPortal } from 'react-dom';

interface OpenCardProps {
    renderCard: (isCardShow: boolean, setIsCardShow: (arg: boolean) => void) => JSX.Element;
}

export function OpenCard({ renderCard, children }: PropsWithChildren<OpenCardProps>): JSX.Element {
    const [isCardShow, setIsCardShow] = useState(false);
    
    return (
        <>
            <div onClick={() => setIsCardShow(true)}>
                {children}
            </div>
            {createPortal(
                renderCard(isCardShow, setIsCardShow),
                document.body
            )}
        </>
    );
}