import React, { JSX, PropsWithChildren } from 'react';
import css from './Card.module.scss';
import cn from 'classnames';
import { Popup } from '5_entities/ui/Popup/Popup';
import { ICard } from '5_entities/cards/types/types';
import { useGetCardQuery } from '5_entities/cards/api/cardsApiSlice';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';

interface CardProps {
    cardId: number;
    isCardShow: boolean;
    setIsCardShow: (arg: boolean) => void;
    renderHead: (cardData: ICard) => JSX.Element;
    renderBody: (cardData: ICard) => JSX.Element;
}

export function Card({ cardId, isCardShow, setIsCardShow, renderHead, renderBody }: CardProps): JSX.Element {
    const { data: cardData, isLoading } = useGetCardQuery(cardId);

    return (
        <Popup toggleShow={isCardShow} onClose={() => setIsCardShow(false)}>
            {isLoading && <SpinerOverlayAbsolute size={1.5} />}
            <Popup.Header>
                {cardData && renderHead(cardData)}
            </Popup.Header>
            <Popup.Body>
                {cardData && renderBody(cardData)}
            </Popup.Body>
        </Popup>
    );
}

interface CardRowProps {
    icon?: JSX.Element
}

Card.Row = function ({ children, icon }: PropsWithChildren<CardRowProps>): JSX.Element {
    return <div className={css.row}>
        {icon && <div className={css.row__icon}>{icon}</div>}
        <div className={css.row__body}>
            {children}
        </div>
    </div>
}