import React, { JSX, useMemo, useState } from 'react';
import css from './Columns.module.scss';

import {
    DndContext,
    DragEndEvent,
    DragOverEvent,
    DragOverlay,
    DragStartEvent,
    PointerSensor,
    useSensor,
    useSensors
} from '@dnd-kit/core';
import { SortableContext, arrayMove } from '@dnd-kit/sortable';

import { CreateColumnButton } from '4_features/createColumn';
import { CreateCardButton } from '4_features/createCard';
import { CardePreviewActions } from '4_features/updateDeletePreviewCard';
import { ColumnHead } from '4_features/updateDeleteColumn';
import { OpenCard } from '4_features/openCard';
import { CardHead } from '4_features/cardHead';
import { CardDescription } from '4_features/cardDescription';
import { CardImages } from '4_features/cardImages';
import { CardComments } from '4_features/cardComments';
import {
    Column,
    ColumnsList,
    ColumnsListPreload,
    updateColumns,
    useGetColumnsQuery,
    useUpdateColumnOrderMutation
} from '5_entities/columns';
import { IColumnWithExtraId } from '5_entities/columns/types/types';
import {
    Card,
    CardPrevie,
    ICardPreview,
    useSetNewColumnCardMutation,
    useSetNewOrderCardMutation
} from '5_entities/cards';
import { useAppDispatch } from '6_shared/store/hooks';
import { ReactComponent as SvgIconCard } from '6_shared/assets/icons/card.svg';
import { ReactComponent as SvgIconBurger } from '6_shared/assets/icons/burger.svg';
import { ReactComponent as SvgIconClip } from '6_shared/assets/icons/clip.svg';
import { ReactComponent as SvgIconComments } from '6_shared/assets/icons/comments.svg';

interface ColumnsProps {
    boardId: number;
}

export function Columns({ boardId }: ColumnsProps): JSX.Element {
    const dispatch = useAppDispatch();
    const [saveColumnsOrder] = useUpdateColumnOrderMutation();
    const [saveCardsOrder] = useSetNewOrderCardMutation();
    const [saveNewColumnCard] = useSetNewColumnCardMutation();

    const { data: columns, isLoading: isColumnsLoading } = useGetColumnsQuery(boardId);
    const columnsIds = useMemo(() => {
        return columns
            ? columns.map(col => col.columnId)
            : []
    }, [columns]);

    const [activeColumn, setActiveColumn] = useState<IColumnWithExtraId | null>(null);
    const [activeCard, setActiveCard] = useState<ICardPreview | null>(null);

    const [initialDataOfTheActiveElement, setInitialDataOfTheActiveElement]
        = useState<{ columnId: number, cardId: number } | null>(null);

    const sensors = useSensors(
        useSensor(PointerSensor, {
            activationConstraint: {
                distance: 3
            }
        })
    )

    return (
        <div className={css.Columns}>
            <DndContext
                sensors={sensors}
                onDragEnd={onDragEndHandler}
                onDragStart={onDragStartHandler}
                onDragOver={onDragOverHandler}
            // collisionDetection={closestCorners}
            >
                <SortableContext items={columnsIds}>
                    {isColumnsLoading
                        ? <ColumnsListPreload />
                        : <ColumnsList>
                            {columns &&
                                columns.map(col => {
                                    return (
                                        <ColumnsList.Item key={col.id}>
                                            <Column
                                                columnData={col}
                                                renderHead={() =>
                                                    <ColumnHead
                                                        boardId={boardId}
                                                        columnId={col.id}
                                                        title={col.name}
                                                    />
                                                }
                                                renderBody={() => <>
                                                    {col.cards.map(card =>
                                                        <OpenCard key={card.cardId}
                                                            renderCard={(isCardShow, setIsCardShow) =>
                                                                <Card
                                                                    cardId={card.cardId}
                                                                    isCardShow={isCardShow}
                                                                    setIsCardShow={setIsCardShow}
                                                                    renderHead={(cardData) =>
                                                                        <Card.Row icon={<SvgIconCard />}>
                                                                            <CardHead
                                                                                boardId={boardId}
                                                                                columnId={col.id}
                                                                                columnTitle={col.name}
                                                                                cardId={card.cardId}
                                                                                cardTitle={card.title}
                                                                            />
                                                                        </Card.Row>
                                                                    }
                                                                    renderBody={(cardData) => <>
                                                                        <Card.Row icon={<SvgIconBurger />}>
                                                                            <h4>Description</h4>
                                                                            <div className="mt-3">
                                                                                <CardDescription
                                                                                    boardId={boardId}
                                                                                    columnId={col.id}
                                                                                    cardId={card.cardId}
                                                                                    cardDescription={card.description}
                                                                                />
                                                                            </div>
                                                                        </Card.Row>
                                                                        <Card.Row icon={<SvgIconClip />}>
                                                                            <h4>Images</h4>
                                                                            <div className="mt-3">
                                                                                <CardImages
                                                                                    cardId={card.cardId}
                                                                                    images={cardData.images}
                                                                                />
                                                                            </div>
                                                                        </Card.Row>
                                                                        <Card.Row icon={<SvgIconComments />}>
                                                                            <h4>Comments</h4>
                                                                            <div className="mt-3">
                                                                                <CardComments
                                                                                    cardId={card.cardId}
                                                                                    comments={cardData.comments}
                                                                                />
                                                                            </div>
                                                                        </Card.Row>
                                                                    </>}
                                                                />
                                                            }
                                                        >
                                                            <CardPrevie key={card.cardId} cardData={card}>
                                                                <CardePreviewActions
                                                                    boardId={boardId}
                                                                    columnId={col.id}
                                                                    cardData={card}
                                                                />
                                                            </CardPrevie>
                                                        </OpenCard>
                                                    )}
                                                </>}
                                                renderFooter={() =>
                                                    <CreateCardButton boardId={boardId} columnId={col.id} />
                                                }
                                            />
                                        </ColumnsList.Item>
                                    );
                                })
                            }
                            <ColumnsList.Item>
                                <CreateColumnButton boardId={boardId} />
                            </ColumnsList.Item>
                        </ColumnsList>
                    }
                </SortableContext>

                <DragOverlay>
                    {activeColumn
                        && <Column
                            columnData={activeColumn}
                            renderHead={() =>
                                <ColumnHead
                                    boardId={boardId}
                                    columnId={activeColumn.id}
                                    title={activeColumn.name}
                                />
                            }
                            renderBody={() => <>
                                {activeColumn.cards.map(card =>
                                    <CardPrevie key={card.cardId} cardData={card}>
                                        <CardePreviewActions
                                            boardId={boardId}
                                            columnId={activeColumn.id}
                                            cardData={card}
                                        />
                                    </CardPrevie>
                                )}
                            </>}
                            renderFooter={() =>
                                <CreateCardButton boardId={boardId} columnId={activeColumn.id} />
                            }
                        />
                    }
                    {activeCard
                        && <CardPrevie cardData={activeCard}>
                            <CardePreviewActions
                                boardId={boardId}
                                columnId={activeCard.columnId}
                                cardData={activeCard}
                            />
                        </CardPrevie>
                    }
                </DragOverlay>
            </DndContext>
        </div>
    );

    function onDragStartHandler(event: DragStartEvent) {
        if (event.active.data.current?.type === 'Column') {
            setActiveColumn(event.active.data.current.column);
            return;
        }
        if (event.active.data.current?.type === 'Card') {
            setActiveCard(event.active.data.current.card);
            return;
        }
    }

    function onDragEndHandler(event: DragEndEvent) {
        setActiveColumn(null);
        setActiveCard(null);

        const { active, over } = event;
        if (!over) return;

        if (!columns) return;

        const isActiveAColumn = active.data.current?.type === 'Column';
        const isOverAColumn = over.data.current?.type === 'Column';
        const isActiveACard = active.data.current?.type === 'Card';
        const isOverACard = over.data.current?.type === 'Card';


        if (isActiveAColumn && isOverAColumn) {
            const activeColumnId = active.id as string;

            const index = columns.findIndex(col => col.columnId === activeColumnId);
            saveColumnsOrder({
                boardId,
                columnId: Number(activeColumnId.replace('column_', '')),
                order: index + 1,
            })
        }

        if (isActiveACard && isOverACard) {
            const activeColumnId = active.data.current?.card.columnId as number;
            const overColumnId = over.data.current?.card.columnId as number;
            const activeCardId = active.id as number;

            if (activeColumnId === overColumnId) {
                const column = columns.find(col => col.id === activeColumnId);
                if (!column) return;
                const index = column.cards.findIndex(card => card.cardId === activeCardId);

                if (
                    initialDataOfTheActiveElement
                    && (initialDataOfTheActiveElement.cardId === activeCardId)
                    && (initialDataOfTheActiveElement.columnId !== activeColumnId)
                ) {
                    saveNewColumnCard({
                        cardId: activeCardId,
                        columnId: activeColumnId,
                        order: index + 1
                    })
                } else {
                    saveCardsOrder({ cardId: activeCardId, order: index + 1 })
                }
            }
        }

        if (isActiveACard && isOverAColumn) {
            const activeCardId = active.id as number;
            const activeColumnId = active.data.current?.card.columnId as number;

            const column = columns.find(col => col.id === activeColumnId);
            if (!column) return;
            const index = column.cards.findIndex(card => card.cardId === activeCardId);

            saveNewColumnCard({
                cardId: activeCardId,
                columnId: activeColumnId,
                order: index + 1
            })
        }

        setInitialDataOfTheActiveElement(null);
    }

    function onDragOverHandler(event: DragOverEvent) {
        const { active, over } = event;
        if (!over) return;

        const activeId = active.id;
        const overId = over.id;

        if (activeId === overId) return;

        if (!columns) return;

        const isActiveACard = active.data.current?.type === 'Card';
        const isOverACard = over.data.current?.type === 'Card';
        const isActiveAColumn = active.data.current?.type === 'Column';
        const isOverAColumn = over.data.current?.type === 'Column';

        if (isActiveACard && isOverACard) {
            const activeColumnId = active.data.current?.card.columnId as number;
            const overColumnId = over.data.current?.card.columnId as number;

            const clonedColumnsData = columns.map(col => {
                return {
                    ...col,
                    cards: [...col.cards]
                }
            });

            if (activeColumnId === overColumnId) {
                const updatedColumnsData = clonedColumnsData.map(col => {
                    if (col.id === activeColumnId) {
                        const oldIndex = col.cards.findIndex(card => card.cardId === activeId);
                        const newIndex = col.cards.findIndex(card => card.cardId === overId);

                        col.cards = arrayMove(col.cards, oldIndex, newIndex);
                    }
                    return col;
                });

                dispatch(
                    updateColumns(
                        boardId,
                        updatedColumnsData
                    )
                )
            } else {
                const updatedColumnsData = clonedColumnsData.map(col => {
                    if (col.id === activeColumnId) {
                        col.cards = col.cards.filter(card => card.cardId !== activeId);
                    } else if (col.id === overColumnId) {
                        const index = col.cards.findIndex(card => card.cardId === overId);
                        const card = {
                            ...active.data.current?.card,
                            columnId: overColumnId
                        };
                        col.cards.splice(index, 0, card)
                    }
                    return col;
                });

                dispatch(
                    updateColumns(
                        boardId,
                        updatedColumnsData
                    )
                )

                setInitialDataOfTheActiveElement({
                    columnId: activeColumnId,
                    cardId: activeId as number,
                })
            }
        }

        if (isActiveACard && isOverAColumn) {
            const overId = over.id as string;
            const activeColumnId = active.data.current?.card.columnId as number;
            const overColumnId = Number(overId.replace('column_', ''));

            if (activeColumnId === overColumnId) return;

            const clonedColumnsData = columns.map(col => {
                return {
                    ...col,
                    cards: [...col.cards]
                }
            });
            const updatedColumnsData = clonedColumnsData.map(col => {
                if (col.id === activeColumnId) {
                    col.cards = col.cards.filter(card => card.cardId !== activeId);
                } else if (col.id === overColumnId) {
                    const card = {
                        ...active.data.current?.card,
                        columnId: overColumnId
                    };
                    col.cards.push(card);
                }
                return col;
            });

            dispatch(
                updateColumns(
                    boardId,
                    updatedColumnsData
                )
            )

            setInitialDataOfTheActiveElement({
                columnId: activeColumnId,
                cardId: activeId as number,
            })
        }

        if (isActiveAColumn && isOverAColumn) {
            const activeColumnIndex = columns.findIndex(col => col.columnId === activeId);
            const overColumnIndex = columns.findIndex(col => col.columnId === overId);

            dispatch(
                updateColumns(
                    boardId,
                    arrayMove(columns, activeColumnIndex, overColumnIndex)
                )
            )
        }
    }
}