import React, { JSX, useState } from 'react';
import css from './CreateColumnButton.module.scss';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import { useCreateColumnMutation } from '5_entities/columns';
import { Box } from '6_shared/ui/Box/Box';
import { Input } from '6_shared/ui/fields/Input';
import { Button } from '6_shared/ui/Button/Button';
import { ReactComponent as SvgIconCLose } from '6_shared/assets/icons/close.svg';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';

interface CreateColumnButtonProps {
    boardId: number;
}

interface FormData {
    name: string;
}

const schema = yup.object().shape({
    name:
        yup.string()
            .min(2, "Name of column must have at least 2 characters")
            .required('Write the column name'),
}).required();

export function CreateColumnButton({ boardId }: CreateColumnButtonProps): JSX.Element {
    const [createColumn, { isLoading }] = useCreateColumnMutation();
    const [editMode, setEditMode] = useState(false);

    const { register, handleSubmit, setValue, formState: { errors } } = useForm<FormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onCreateColumn = async (data: FormData) => {
        try {
            await createColumn({ boardId, name: data.name }).unwrap();
            setValue('name', '');
            setEditMode(false);
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <div className={css.CreateColumnButton}>
            {isLoading && <SpinerOverlayAbsolute size={1} />}
            <div
                onClick={() => setEditMode(true)}
                className={css.CreateColumnButton__inner}
            >

                {!editMode
                    ? <Box padding={2} className={css.CreateColumnButton__box}>
                        Create Column
                    </Box>
                    : <Box padding={2}>
                        <form onSubmit={handleSubmit(onCreateColumn)}>
                            <Input
                                placeholder="Enter column title"
                                {...register('name')}
                                errorState={errors.name ? true : false}
                                autoFocus
                            />
                            {errors.name &&
                                <div className="mt-2 text-size-2 text-color-error text-start">
                                    {errors.name.message}
                                </div>
                            }
                            <div className="d-flex gap-2 align-items-center mt-2">
                                <Button type='submit'>Create column</Button>
                                <Button
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        setEditMode(false);
                                    }}
                                    type='button'
                                    variant='transparent'
                                >
                                    <SvgIconCLose />
                                </Button>
                            </div>
                        </form>
                    </Box>
                }

            </div>
        </div>
    );
}