import React, { JSX, useMemo } from 'react';
import css from './Column.module.scss';
import cn from 'classnames';
import { SortableContext, useSortable } from '@dnd-kit/sortable';

import { IColumnWithExtraId } from '5_entities/columns/types/types';

interface ColumnProps {
    columnData: IColumnWithExtraId;
    renderHead: () => JSX.Element;
    renderBody: () => JSX.Element;
    renderFooter: () => JSX.Element;
}

export function Column({ columnData, renderHead, renderBody, renderFooter }: ColumnProps): JSX.Element {
    const {
        attributes,
        listeners,
        setNodeRef,
        isDragging,
    } = useSortable({
        id: columnData.columnId,
        data: {
            type: 'Column',
            column: columnData
        }
    });

    const cardIds = useMemo(() => {
        return columnData?.cards
            ? columnData?.cards.map(card => card.cardId)
            : []
    }, [columnData?.cards]);

    return (
        <div className={cn(css.column)} ref={setNodeRef}>
            <div className={cn(css.column__inner, { [css.dragging]: isDragging })}>
                <div className={css.column__head} {...attributes} {...listeners}>
                    {renderHead()}
                </div>
                <div className={css.column__body}>
                    <SortableContext items={cardIds}>
                        {renderBody()}
                    </SortableContext>
                </div>
                <div className={css.column__footer}>{renderFooter()}</div>
            </div>
        </div>
    );

}