import React, { JSX, PropsWithChildren } from 'react';
import css from './ColumnsList.module.scss';
import cn from 'classnames';

interface ColumnsListProps {

}

export function ColumnsList( { children }: PropsWithChildren<ColumnsListProps>): JSX.Element {
    return (
        <ul className={css.ColumnsList}>
            {children}
        </ul>
    );
}

ColumnsList.Item = function({ children }: PropsWithChildren<ColumnsListProps>): JSX.Element {
    return (
        <li>{children}</li>
    );
}