import React, { HtmlHTMLAttributes, JSX, ReactElement, ReactNode } from 'react';
import css from './Auth.module.scss';
import cn from 'classnames';

interface AuthContainerProps extends HtmlHTMLAttributes<ReactElement | ReactNode>  { }

export function AuthContainer( { children }: AuthContainerProps ): JSX.Element {
    return (
        <div className={css.auth_container}>
            {children}
        </div>
    );
}