import React, { JSX } from 'react';

import { CreateBoard } from '4_features/createBoard';
import {
    BoardListLoader,
    BoardsList,
    useGetOwnBoardsQuery,
    useGetGuestBoardsQuery
} from '5_entities/boards';
import { useGetMeQuery } from '5_entities/profile';


export function Boards(): JSX.Element {
    const { data: me } = useGetMeQuery();

    const {
        data: ownBoards,
        isLoading: isOwnBoardsLoading
    } = useGetOwnBoardsQuery({});

    const {
        data: guestBoards,
        isLoading: isGuestBoardsLoading
    } = useGetGuestBoardsQuery({ meId: me?.id as number });

    return (
        <div className="container">
            <h2>Your Workspaces</h2>
            <div className="mt-3">
                {isOwnBoardsLoading
                    ? <BoardListLoader />
                    : ownBoards ? <BoardsList
                        variant='grid'
                        boards={ownBoards}
                        lastItemSlot={<CreateBoard />}
                    /> : 'Empty'
                }
            </div>
            <div className="mt-5">
                <h2>Guest Workspaces</h2>
                <div className="mt-3">
                    {isGuestBoardsLoading
                        ? <BoardListLoader />
                        : guestBoards && guestBoards.length ? <BoardsList
                            variant='grid'
                            boards={guestBoards}
                        /> : 'Empty'
                    }
                </div>
            </div>
        </div>
    );
}