import React, { JSX, useState } from 'react';
import css from './InviteUser.module.scss';
import cn from 'classnames';
import { createPortal } from 'react-dom';
import Fuse from 'fuse.js';

import { useAddMemberMutation, useGetMembersQuery } from '4_features/members/api/membersApiSlice';
import { Popup } from '5_entities/ui/Popup/Popup';
import { useGetAllUsersQuery } from '5_entities/users/api/usersApiSlice';
import { useGetMeQuery } from '5_entities/profile';
import { Avatar } from '5_entities/ui/Avatar/Avatar';
import { ReactComponent as SvgIconPlus } from '6_shared/assets/icons/plus.svg';
import { ReactComponent as SvgIconCheck } from '6_shared/assets/icons/check.svg';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';
import { Input } from '6_shared/ui/fields/Input';

interface InviteUserProps {
    boardId: number;
    isShow: boolean;
    setShow: (arg: boolean) => void;
}

export function InviteUser({ boardId, isShow, setShow }: InviteUserProps): JSX.Element {
    const { data: me } = useGetMeQuery();
    const { data: users, isLoading } = useGetAllUsersQuery(me?.id as number);
    const {
        data: members,
        isLoading: isMembersLoading,
        isFetching
    } = useGetMembersQuery({ meId: Number(me?.id), boardId });
    const [addMember, { isLoading: isAddMemberLoading }] = useAddMemberMutation();
    const [inputSearchValue, setInputSearchValue] = useState<string>('');

    const fuse = new Fuse(users || [], {
        keys: [{ name: 'login', weight: 2 }, 'email'],
        threshold: 0.35
    });

    const filteredUsers = inputSearchValue.length
        ? fuse.search(inputSearchValue).map(fuse => fuse.item)
        : users;

    return (
        createPortal(
            <>
                <Popup toggleShow={isShow} onClose={() => setShow(false)}>
                    {(isLoading || isAddMemberLoading || isFetching || isMembersLoading)
                        && <SpinerOverlay zIndex={10} size={1.5} />
                    }
                    <Popup.Header>
                        <h3>Inviting Users</h3>
                    </Popup.Header>
                    <Popup.Body>
                        <Input
                            value={inputSearchValue}
                            onChange={(e) => setInputSearchValue(e.target.value)}
                            placeholder='Search'
                            errorState={false}
                        />
                        <div className="mt-3">
                            <ul className={css.user_list}>
                                {filteredUsers && members && filteredUsers.map(user => {
                                    return (<li key={user.id}>
                                        <div className={css.user}>
                                            <div className={css.user__photo}>
                                                <Avatar src={user.profilePhoto} />
                                            </div>
                                            <div className={css.user__info}>
                                                <div className={css.user__login}>{user.login}</div>
                                                <div className={css.user__email}>{user.email}</div>
                                            </div>
                                            {members.users.find((m) => m.id === user.id)
                                                ? <div className={css.user__icon}>
                                                    <SvgIconCheck />
                                                </div>
                                                : <button
                                                    disabled={isAddMemberLoading || isFetching || isMembersLoading}
                                                    onClick={() => addMember({ boardId, userId: user.id })}
                                                    className={cn(css.user__btn, css.user__icon)}
                                                >
                                                    <SvgIconPlus />
                                                </button>
                                            }
                                        </div>
                                    </li>)
                                })}
                            </ul>
                        </div>
                    </Popup.Body>
                </Popup>
            </>,
            document.body
        )
    );
}