import React, { JSX, useEffect, useState } from 'react';
import css from './Board.module.scss';
import cn from 'classnames';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { createPortal } from 'react-dom';

import { Columns } from '../Columns/Columns';

import { BoardOptions } from '4_features/boardOptions';
import { Members } from '4_features/members';
import {
    BoardsList,
    BoardsWsTypesOfMessages,
    useGetBoardQuery,
    useGetOwnBoardsQuery
} from '5_entities/boards';
import { useGetMeQuery } from '5_entities/profile';
import { Popup } from '5_entities/ui/Popup/Popup';
import { ReactComponent as SvgBoardsIcon } from '6_shared/assets/icons/boards.svg';
import { ReactComponent as SvgUserIcon } from '6_shared/assets/icons/user.svg';
import { ReactComponent as SvgGearIcon } from '6_shared/assets/icons/gear.svg';
import { AppWebsocket } from '6_shared/websocket';
import { Button } from '6_shared/ui/Button/Button';

export function Board(): JSX.Element {
    const params = useParams<{ id: string }>();
    const id = Number(params.id);
    const navigate = useNavigate();
    const [isBoardOptionsShow, setIsBoardOptionsShow] = useState<boolean>(false);
    const [isMembersShow, setIsMembersShow] = useState<boolean>(false);
    const { data: board, isError } = useGetBoardQuery(id);
    const { data: ownBoards } = useGetOwnBoardsQuery({});
    const { data: me } = useGetMeQuery();
    const isGuest: boolean = !!board && board.createdById !== me?.id;
    const [isPopupShow, setIsPopupShow] = useState(false);
    const [wsActions, setWSActions] = useState<'deleteBoard' | 'removeParticipantFromBoard' | null>(null);

    useEffect(() => {
        if (isError) {
            navigate('/boards');
        }
    }, [navigate, isError]);

    useEffect(() => {

        const onDeleteBoard = (_: WebSocket, data: { boardId: number, message: string }) => {
            setIsPopupShow(true);
            setWSActions('deleteBoard');
        }

        const onRemoveParticipantFromBoard = (_: WebSocket, data: { removedUserId: number }) => {
            if (me?.id === data.removedUserId) {
                setIsPopupShow(true);
                setWSActions('removeParticipantFromBoard');
            }
        }

        AppWebsocket.subscribe(
            BoardsWsTypesOfMessages.deleteBoard,
            onDeleteBoard
        );
        AppWebsocket.subscribe(
            BoardsWsTypesOfMessages.removeParticipantFromBoard,
            onRemoveParticipantFromBoard
        );

        return () => {
            AppWebsocket.unsubscribe(
                BoardsWsTypesOfMessages.deleteBoard,
                onDeleteBoard
            );
            AppWebsocket.unsubscribe(
                BoardsWsTypesOfMessages.removeParticipantFromBoard,
                onRemoveParticipantFromBoard
            );
        }

    }, [me, navigate]);

    return (
        <>
            <div className={css.board}>
                <div className={cn(css.board__side, css.side)}>
                    <div className={css.side__head}>
                        {board?.name}
                    </div>
                    <div className={css.side__body}>
                        <div className={css.side__row}>
                            <ul className={css.side__list}>
                                <li>
                                    <Link to='/boards' className={css.side__list_item}>
                                        <SvgBoardsIcon />
                                        Boards
                                    </Link>
                                </li>
                                <li>
                                    <button
                                        className={css.side__list_item}
                                        onClick={() => setIsMembersShow(true)}
                                    >
                                        <SvgUserIcon />
                                        Members
                                    </button>
                                    <Members
                                        isGuest={isGuest}
                                        boardId={id}
                                        isMembersShow={isMembersShow}
                                        setMembersShow={setIsMembersShow}
                                    />
                                </li>
                                {!isGuest &&
                                    <li>
                                        <button
                                            className={css.side__list_item}
                                            onClick={() => setIsBoardOptionsShow(true)}
                                        >
                                            <SvgGearIcon />
                                            Options
                                        </button>
                                        <BoardOptions
                                            isShow={isBoardOptionsShow}
                                            setIsShow={setIsBoardOptionsShow}
                                            boardId={id}
                                        />
                                    </li>
                                }
                            </ul>
                        </div>
                        <div className={css.side__row}>
                            <h5 className='px-2'>My boards</h5>
                            <div className="mt-1">
                                {ownBoards && <BoardsList boards={ownBoards} variant='list' />}
                            </div>
                        </div>
                    </div>
                </div>
                <div className={css.board__columns}>
                    <Columns boardId={id} />
                </div>
            </div>
            {createPortal(
                <Popup toggleShow={isPopupShow} onClose={() => navigate('/boards')}>
                    <Popup.Body>
                        <div className="py-4">
                            {(wsActions === 'deleteBoard')
                                && <h3 className='text-center'>
                                    This board has just been removed by the owner.
                                </h3>}
                            {(wsActions === 'removeParticipantFromBoard')
                                && <h3 className='text-center'>
                                    You have just been removed from the board by the owner.
                                </h3>}
                            <div className="text-center mt-3">
                                <Button
                                    onClick={() => navigate('/boards')}
                                >
                                    Open the board page
                                </Button>
                            </div>
                        </div>
                    </Popup.Body>
                </Popup>
                ,
                document.body
            )}
        </>
    );
}