import React, { JSX, useState } from 'react';
import css from './ColumnHead.module.scss';

import { useDeleteColumnMutation, useUpdateColumnMutation } from '5_entities/columns';
import { Button } from '6_shared/ui/Button/Button';
import { ReactComponent as SvgIconDelete } from '6_shared/assets/icons/delete.svg'
import { Input } from '6_shared/ui/fields/Input';
import { PopOver } from '6_shared/ui/PopOver/PopOver';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';

interface ColumnHeadProps {
    boardId: number;
    columnId: number;
    title: string;
}

export function ColumnHead({ boardId, columnId, title }: ColumnHeadProps): JSX.Element {
    const [editMode, setEditMode] = useState(false);
    const [inputValue, setInputValue] = useState(title);
    const [updateColumn] = useUpdateColumnMutation();
    const [deleteColumn, { isLoading: isColumDeleting }] = useDeleteColumnMutation();

    const onBlurHandler = () => {
        setEditMode(false);
        if (inputValue === title || inputValue.trim().length === 0) {
            setInputValue(title);
        } else {
            updateColumn({ boardId, columnId, name: inputValue });
        }
    }

    const onDeleteColumnHandler = () => {
        deleteColumn({ boardId, columnId });
    }

    return (
        <div className={css.ColumnHead}>
            <div className={css.ColumnHead__title} onClick={() => setEditMode(true)}>
                {!editMode
                    ? <p>{title}</p>
                    : <Input
                        value={inputValue}
                        onChange={(e) => setInputValue(e.target.value)}
                        autoFocus
                        errorState={false}
                        onBlur={onBlurHandler}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter') onBlurHandler();
                        }}
                    />
                }
            </div>
            <div className={css.ColumnHead__button}>
                <PopOver
                    mode='inline'
                    title={<h4>Deleting a column</h4>}
                    content={
                        <>
                            {isColumDeleting && <SpinerOverlayAbsolute size={1} />}
                            <p>Are you sure? The column will be deleted permanently!</p>
                            <Button
                                variant='danger'
                                onClick={onDeleteColumnHandler}
                                style={{ width: '100%' }}
                            >
                                Delete Column
                            </Button>
                        </>
                    }
                >
                    <Button variant='transparent'>
                        <SvgIconDelete />
                    </Button>
                </PopOver>
            </div>
        </div>
    );
}