import React, { JSX, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { createPortal } from 'react-dom';
import css from './CreateBoard.module.scss';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";


import { Popup } from '5_entities/ui/Popup/Popup';
import { ICreateBoardFormData } from '5_entities/boards/types/types';
import { useCreateBoardMutation } from '5_entities/boards';
import { Input } from '6_shared/ui/fields/Input';
import { Button } from '6_shared/ui/Button/Button';
import { Dropzone } from '6_shared/ui/Dropzone/Dropzone';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';

const schema = yup.object().shape({
    name:
        yup.string()
            .min(2, "Name must have at least 2 characters")
            .required('Name is a required field'),
    photo: yup.mixed()
}).required();


export function CreateBoard(): JSX.Element {
    const [createBoard, { isLoading }] = useCreateBoardMutation();
    const [isPopupShow, setIsPopupShow] = useState<boolean>(false);
    const navigate = useNavigate();
    const {
        register, handleSubmit,
        formState: { errors },
        control,
        setFocus
    } = useForm<ICreateBoardFormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onSubmit = async (data: ICreateBoardFormData) => {
        try {
            let response = await createBoard(data).unwrap();
            navigate('/board/' + response.data.id);
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <>
            <button
                className={css.createBoard}
                onClick={() => setIsPopupShow(true)}
            >
                Create Board
            </button>

            {createPortal(
                <>
                    {isLoading && <SpinerOverlay zIndex={10} size={1.5} />}
                    <Popup
                        toggleShow={isPopupShow}
                        onClose={() => setIsPopupShow(false)}
                        componentDidMount={() => setFocus('name')}
                    >
                        <Popup.Header>
                            <h3>Creat new board</h3>
                        </Popup.Header>
                        <Popup.Body>
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <div>
                                    <h5>Board Name</h5>
                                    <div className="mt-1">
                                        <Input
                                            type='text'
                                            placeholder='Board Name'
                                            {...register('name')}
                                            errorState={errors.name ? true : false}
                                        />
                                        {errors.name &&
                                            <div className="mt-2 text-size-2 text-color-error">
                                                {errors.name.message}
                                            </div>
                                        }
                                    </div>
                                </div>
                                <div className="mt-3">
                                    <h5>Board Background Image</h5>
                                    <div className="mt-1">
                                        <Dropzone control={control} name={'photo'} multiple={false} />
                                    </div>
                                </div>
                                <div className="mt-3">
                                    <Button>Create</Button>
                                </div>
                            </form>
                        </Popup.Body>
                    </Popup>
                </>,
                document.body
            )}
        </>
    );
}