import React, { JSX } from 'react';

import { createPortal } from 'react-dom';
import { useNavigate } from 'react-router-dom';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";


import { Popup } from '5_entities/ui/Popup/Popup';
import {
    ICreateBoardFormData,
    IUpdateBoardFormData,
    useDeleteBoardMutation,
    useGetBoardQuery,
    useUpdateBoardMutation
} from '5_entities/boards';
import { Input } from '6_shared/ui/fields/Input';
import { Button } from '6_shared/ui/Button/Button';
import { Dropzone } from '6_shared/ui/Dropzone/Dropzone';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';
import { PopOver } from '6_shared/ui/PopOver/PopOver';

const schema = yup.object().shape({
    name:
        yup.string()
            .min(2, "Name must have at least 2 characters")
            .required('Name is a required field'),
    photo: yup.mixed()
}).required();

interface BoardOptionsProps {
    boardId: number;
    isShow: boolean;
    setIsShow: (arg: boolean) => void;
}

export function BoardOptions({ boardId, isShow, setIsShow }: BoardOptionsProps): JSX.Element {

    const [updateBoard, { isLoading }] = useUpdateBoardMutation();
    const [deleteBoard, { isLoading: isBoardDeletign }] = useDeleteBoardMutation();
    const { data: board } = useGetBoardQuery(boardId);
    const navigate = useNavigate();
    const {
        register,
        handleSubmit,
        formState: { errors, isDirty },
        control
    } = useForm<ICreateBoardFormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onSubmit = async (data: IUpdateBoardFormData) => {
        try {
            updateBoard({ id: boardId, body: data }).unwrap();
        } catch (error) {
            console.log(error);
        }
    }

    const deleteBoardHandler = async () => {
        try {
            await deleteBoard(boardId).unwrap();
            navigate('/boards');
        } catch (error) {
            console.log(error);
        }
    }

    return (createPortal(
        <>
            {(isLoading || isBoardDeletign) && <SpinerOverlay zIndex={10} size={1.5} />}
            <Popup toggleShow={isShow} onClose={() => setIsShow(false)} >
                <Popup.Header>
                    <h3>Board options</h3>
                </Popup.Header>
                <Popup.Body>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div>
                            <h5>Change Name</h5>
                            <div className="mt-1">
                                <Input
                                    type='text'
                                    placeholder='Board Name'
                                    defaultValue={board?.name}
                                    {...register('name')}
                                    errorState={errors.name ? true : false}
                                />
                                {errors.name &&
                                    <div className="mt-2 text-size-2 text-color-error">
                                        {errors.name.message}
                                    </div>
                                }
                            </div>
                        </div>
                        <div className="mt-3">
                            <h5>{board?.previewImage
                                ? 'Upload new background image'
                                : 'Upload background image'}
                            </h5>
                            <div className="mt-1">
                                <Dropzone control={control} name={'photo'} multiple={false} />
                            </div>
                        </div>
                        <div className="mt-3">
                            <Button disabled={!isDirty} >Update</Button>
                        </div>
                    </form>
                    <div className="mt-4">
                        <PopOver
                            mode='inline'
                            title={<h4>Deleting a board</h4>}
                            content={
                                <>
                                    <p>Are you sure, the board will be deleted permanently?</p>
                                    <Button
                                        variant='danger'
                                        style={{ width: '100%' }}
                                        onClick={deleteBoardHandler}
                                    >Delete board</Button>
                                </>
                            }
                        >
                            <Button variant='danger'>Delete board</Button>
                        </PopOver>
                    </div>
                </Popup.Body>
            </Popup>
        </>,
        document.body
    ));
}