import { apiSlice } from "6_shared/api/apiSlice";
import { rquestWithJwtToken } from "6_shared/helpers/helpers";
import {
    CardWsTypesOfMessages,
    CreateCardArgs,
    DeleteCardArgs,
    ICreateCardResponse,
    IGetCardResponse,
    IUpdateCardResponse,
    IWsMessageCardData,
    SetNewColumnCardArgs,
    SetNewOrderCardArgs,
    UpdateCardArgs
} from "../types/types";
import { AppWebsocket } from "6_shared/websocket";

export const cardsApi = apiSlice.injectEndpoints({
    endpoints: (builder) => ({

        createCard: builder.mutation<ICreateCardResponse, CreateCardArgs>({
            query: ({ columnId, title }) =>
                rquestWithJwtToken({ url: '/cards', method: 'POST', body: { columnId, title } }),
            async onQueryStarted(arg, { queryFulfilled }) {
                try {
                    const response = await queryFulfilled;
                    arg.onQueryFulfilled(arg.boardId, arg.columnId, response.data.data);
                } catch (error) {
                    console.log(error);
                }
            }
        }),

        updateCard: builder.mutation<IUpdateCardResponse, UpdateCardArgs>({
            query: ({ cardId, body }) =>
                rquestWithJwtToken({ url: `/cards/${cardId}`, method: 'PUT', body }),
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                const patchResult = arg.onBeforeQueryFulfilled(dispatch);
                try {
                    await queryFulfilled;
                } catch (error) {
                    console.log(error);
                    patchResult.undo();
                }
            }
        }),

        setNewOrderCard: builder.mutation<IUpdateCardResponse, SetNewOrderCardArgs>({
            query: ({ cardId, order }) =>
                rquestWithJwtToken({ url: `/cards/${cardId}`, method: 'PUT', body: { order } })
        }),

        setNewColumnCard: builder.mutation<IUpdateCardResponse, SetNewColumnCardArgs>({
            query: ({ cardId, columnId, order }) =>
                rquestWithJwtToken({ url: `/cards/${cardId}/column`, method: 'PUT', body: { order, columnId } })
        }),

        deleteCard: builder.mutation<any, DeleteCardArgs>({
            query: ({ cardId }) =>
                rquestWithJwtToken({ url: `/cards/${cardId}`, method: 'DELETE' }),
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    await queryFulfilled;
                    arg.onQueryFulfilled(dispatch);
                } catch (error) {
                    console.log(error);
                }
            }
        }),
        
        getCard: builder.query({
            query: (cardId: number) => rquestWithJwtToken({ url: `/cards/${cardId}` }),
            transformResponse: (responseData: IGetCardResponse) => {
                return responseData.data;
            },
            providesTags: (result, error, arg) => [{ type: 'Card', cardId: arg }],
            async onCacheEntryAdded(arg, { updateCachedData, cacheDataLoaded, cacheEntryRemoved, dispatch }) {
                const onCardWSAction = (_: WebSocket, data: IWsMessageCardData) => {
                    dispatch(
                        cardsApi.util.upsertQueryData('getCard', arg, data)
                    )
                }
                try {
                    await cacheDataLoaded;
                    AppWebsocket.subscribe(CardWsTypesOfMessages.uploadImages, onCardWSAction);
                    AppWebsocket.subscribe(CardWsTypesOfMessages.deleteCardImage, onCardWSAction);
                    AppWebsocket.subscribe(CardWsTypesOfMessages.createCardComment, onCardWSAction);
                    AppWebsocket.subscribe(CardWsTypesOfMessages.updateCardComment, onCardWSAction);
                    AppWebsocket.subscribe(CardWsTypesOfMessages.deleteCardComment, onCardWSAction);
                } catch (error) {
                    console.log(error);
                }

                await cacheEntryRemoved;
                AppWebsocket.unsubscribe(CardWsTypesOfMessages.uploadImages, onCardWSAction);
                AppWebsocket.unsubscribe(CardWsTypesOfMessages.deleteCardImage, onCardWSAction);
                AppWebsocket.unsubscribe(CardWsTypesOfMessages.createCardComment, onCardWSAction);
                AppWebsocket.unsubscribe(CardWsTypesOfMessages.updateCardComment, onCardWSAction);
                AppWebsocket.unsubscribe(CardWsTypesOfMessages.deleteCardComment, onCardWSAction);
            }
        }),

        uploadImages: builder.mutation<null, { cardId: number, images: File[] }>({
            query: ({ cardId, images }) => {
                const formData = new FormData();
                images.forEach(img => {
                    formData.append('images', img);
                })

                return rquestWithJwtToken({ url: `/cards/${cardId}/images`, method: 'POST', body: formData })
            },
            invalidatesTags: (result, error, arg) => [{ type: 'Card', cardId: arg.cardId }, 'Columns']
        }),

        deletaImage: builder.mutation<null, { cardId: number, imageId: number }>({
            query: ({ imageId }) =>
                rquestWithJwtToken({ url: `/cards/image/${imageId}`, method: 'DELETE' }),
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    await queryFulfilled;
                    dispatch(
                        cardsApi.util.updateQueryData('getCard', arg.cardId, (draftCard) => {
                            const indexOfImage = draftCard.images.findIndex(img => img.id === arg.imageId);
                            draftCard.images.splice(indexOfImage, 1);
                        })
                    )
                } catch (error) {
                    console.log(error);
                }
            },
            invalidatesTags: (result, error, arg) => ['Columns']
        }),

        createComment: builder.mutation<null, { cardId: number, text: string }>({
            query: ({ cardId, text }) =>
                rquestWithJwtToken({ url: `/cards/${cardId}/comment`, method: 'POST', body: { text } }),
            invalidatesTags: (result, error, arg) => [{ type: 'Card', cardId: arg.cardId }]
        }),

        updateComment: builder.mutation<null, { cardId: number, commentId: number, text: string }>({
            query: ({ commentId, text }) =>
                rquestWithJwtToken({ url: `/cards/comment/${commentId}`, method: 'PUT', body: { text } }),
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                const patchResult = dispatch(
                    cardsApi.util.updateQueryData('getCard', arg.cardId, (draftCard) => {
                        draftCard.comments.forEach(comment => {
                            if (comment.id === arg.commentId) {
                                comment.text = arg.text
                            }
                        })
                    })
                );

                try {
                    await queryFulfilled;
                } catch (error) {
                    patchResult.undo();
                }
            }
        }),

        deleteComment: builder.mutation<null, { cardId: number, commentId: number }>({
            query: ({ commentId }) =>
                rquestWithJwtToken({ url: `/cards/comment/${commentId}`, method: 'DELETE' }),
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    await queryFulfilled;
                    dispatch(
                        cardsApi.util.updateQueryData('getCard', arg.cardId, (draftCard) => {
                            const index = draftCard.comments.findIndex(c => c.id === arg.commentId);
                            draftCard.comments.splice(index, 1);
                        })
                    );
                } catch (error) {
                    console.log(error)
                }
            }
        }),
        
        updateCommentReadStatus: builder.mutation<null, { cardId: number, statusId: number }>({
            query: ({ statusId }) =>
                rquestWithJwtToken({ url: `/cards/comment/status/${statusId}`, method: 'PUT' }),
            invalidatesTags: (result, error, arg) => [{ type: 'Card', cardId: arg.cardId }, 'Columns']
        }),
    })
});

export const {
    useCreateCardMutation,
    useUpdateCardMutation,
    useDeleteCardMutation,
    useSetNewOrderCardMutation,
    useSetNewColumnCardMutation,
    useGetCardQuery,
    useUploadImagesMutation,
    useDeletaImageMutation,
    useCreateCommentMutation,
    useUpdateCommentMutation,
    useDeleteCommentMutation,
    useUpdateCommentReadStatusMutation,
} = cardsApi;
