import React, { JSX } from 'react';
import css from './BoardPage.module.scss';
import { useParams } from 'react-router-dom';

import { Header } from '3_widgets/header';
import { Board } from '3_widgets/board';
import { useGetBoardQuery } from '5_entities/boards';
import { ServerImg } from '6_shared/ui/ServerImg/ServerImg';
import defaultItemBg from '6_shared/assets/images/gradient-bg.svg';


export function BoardPage(): JSX.Element {
    const params = useParams<{ id: string }>();
    const id = Number(params.id);
    const { data: board } = useGetBoardQuery(id);

    return (
        <div className={css.board}>
            <div className={css.board__bg}>
                {board?.previewImage
                    ? <ServerImg src={board.previewImage} />
                    : <img src={defaultItemBg} alt="" />
                }
            </div>
            <div className={css.board__inner}>
                <div className={css.board__head}>
                    <Header variant='dark' />
                </div>
                <div className={css.board__body}>
                    <Board />
                </div>
            </div>
        </div>
    );
}

export default BoardPage;