import React, { JSX, useEffect, useMemo, useRef, useState } from 'react';
import css from './CardComments.module.scss';
import cn from 'classnames';

import {
    IComment,
    useCreateCommentMutation,
    useDeleteCommentMutation,
    useUpdateCommentMutation,
    useUpdateCommentReadStatusMutation
} from '5_entities/cards';
import { Avatar } from '5_entities/ui/Avatar/Avatar';
import { useGetMeQuery } from '5_entities/profile';
import { TextareaAutoHeight } from '6_shared/ui/fields/TextareaAutoHeight';
import { Button } from '6_shared/ui/Button/Button';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';
import { ReactComponent as SvgIconClose } from '6_shared/assets/icons/close.svg'
import { PopOver } from '6_shared/ui/PopOver/PopOver';

interface CardCommentsProps {
    cardId: number;
    comments: IComment[];
}

export function CardComments({ cardId, comments }: CardCommentsProps): JSX.Element {
    const [createComment, { isLoading }] = useCreateCommentMutation();
    const [textareaValue, setTextareaValue] = useState('');

    const sendCommentHandler = async () => {
        await createComment({ cardId, text: textareaValue });
        setTextareaValue('');
    }

    return (
        <div className={css.CardComments}>
            <div className={cn(css.CardComments__top, css.writeCommentField)}>
                <div className={css.writeCommentField__avatar}>
                    <Avatar src={undefined} />
                </div>
                <div className={css.writeCommentField__field}>
                    {isLoading && <SpinerOverlayAbsolute size={1.5} />}
                    <TextareaAutoHeight
                        value={textareaValue}
                        setValue={setTextareaValue}
                        rows={2}
                        className={css.writeCommentField__textarea}
                        placeholder='Write a comment...'
                    />
                    <div className="mt-2">
                        <Button
                            disabled={!(textareaValue.length > 0)}
                            onClick={sendCommentHandler}
                        >Send</Button>
                    </div>
                </div>
            </div>
            <div className={css.CardComments__list}>
                {comments.map(comment =>
                    <Comment key={comment.id} comment={comment} />
                )}
            </div>
        </div>
    );
}

function Comment({ comment }: { comment: IComment }): JSX.Element {
    const [editMode, setEditMode] = useState(false);
    const [textareaValue, setTextareaValue] = useState(comment.text);
    const [updateComment] = useUpdateCommentMutation();
    const [deleteComment, { isLoading: isCommentDeleting }] = useDeleteCommentMutation();
    const [setAsRead] = useUpdateCommentReadStatusMutation();
    const { data: me } = useGetMeQuery();
    const commentRef = useRef<HTMLDivElement>(null);

    const statusData = useMemo(() => {
        return comment.readStatuses
            .filter(status => status.userId === me?.id)[0] || { isRead: true };
    }, [comment, me])

    const updateCommentHandler = () => {
        setEditMode(false);
        updateComment({
            cardId: comment.cardId,
            commentId: comment.id,
            text: textareaValue
        });
    }

    const onDeleteCommentHandler = () => {
        deleteComment({
            cardId: comment.cardId,
            commentId: comment.id
        })
    }

    useEffect(() => {
        const obeserver = new IntersectionObserver((entries, obeserver) => {
            if (entries[0].isIntersecting) {
                !!statusData?.id && setTimeout(() => {
                    statusData && setAsRead({
                        cardId: comment.cardId,
                        statusId: statusData.id
                    })
                }, 2000)
                obeserver.disconnect();
            }
        })

        if (commentRef.current && !!statusData?.id && !statusData.isRead) {
            obeserver.observe(commentRef.current);
        }

        return () => obeserver.disconnect();
    }, [statusData])

    return (
        <div ref={commentRef} className={css.comment}>
            <div className={css.comment__avatar}>
                <Avatar src={comment.user.profilePhoto} />
            </div>
            <div className={css.comment__body}>
                <div className={css.comment__info}>
                    <div className={css.comment__name}>{comment.user.login}</div>
                    <div className={css.comment__date}>
                        {new Date(comment.createdAt).toLocaleString()}
                    </div>
                    {(statusData ? !statusData?.isRead : true)
                        && <div className={css.comment__readStatus}>NEW</div>
                    }
                </div>
                {!editMode
                    ? <>
                        <div className={css.comment__text}>{comment.text}</div>
                        {me && me.id === comment.userId
                            && <div className={css.comment__actions}>
                                <button
                                    className={css.comment__btn}
                                    onClick={() => setEditMode(true)}
                                >
                                    Edit
                                </button>
                                <PopOver
                                    mode='inline'
                                    title={<h4>Deleting a comment</h4>}
                                    content={
                                        <>
                                            {isCommentDeleting && <SpinerOverlayAbsolute size={1} />}
                                            <p>Are you sure? The comment will be deleted permanently!</p>
                                            <Button
                                                variant='danger'
                                                onClick={onDeleteCommentHandler}
                                                style={{ width: '100%' }}
                                            >
                                                Delete Comment
                                            </Button>
                                        </>
                                    }
                                >
                                    <button className={css.comment__btn}>Delete</button>
                                </PopOver>
                            </div>
                        }

                    </>
                    : <div>
                        <TextareaAutoHeight
                            value={textareaValue}
                            setValue={setTextareaValue}
                            rows={2}
                            className={css.writeCommentField__textarea}
                            autoFocus
                            onFocus={(e) => e.target.selectionStart = e.target.value.length}
                        ></TextareaAutoHeight>
                        <div className="mt-2 d-flex gap-2">
                            <Button
                                disabled={(textareaValue === comment.text) || !(textareaValue.length > 0)}
                                onClick={updateCommentHandler}
                            >Update</Button>
                            <Button variant='transparent' onClick={() => setEditMode(false)}>
                                <SvgIconClose />
                            </Button>
                        </div>
                    </div>
                }
            </div>
        </div>
    );
}