import React, { JSX, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import css from './Auth.module.scss';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import { AuthApi } from '../api/AuthApi';
import { RegisterFormData } from '../types/types';

import { Box } from '6_shared/ui/Box/Box';
import { Input } from '6_shared/ui/fields/Input';
import { InputPassword } from '6_shared/ui/fields/InputPassword';
import { Button } from '6_shared/ui/Button/Button';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';


const getCharacterValidationError = (str: string): string => {
    return `Your password must have at least 1 ${str} character`;
};

const schema = yup.object().shape({
    email: yup.string().email().required(),
    login: yup.string().min(2).required(),
    password: yup.string()
        .matches(/[a-zA-Z0-9]/, 'Password can only contain Latin letters and numbers.')
        .matches(/[0-9]/, getCharacterValidationError("digit"))
        .matches(/[a-z]/, getCharacterValidationError("lowercase"))
        .matches(/[A-Z]/, getCharacterValidationError("uppercase"))
        .matches(/[#|$|%|*]/, getCharacterValidationError("special # $ % *"))
        .min(8, "Password must have at least 8 characters")
        .required(),
    confirm_password:
        yup.string()
            .oneOf([yup.ref('password')], 'Password does not match')
            .required()
}).required();

// petya@gmail.com
// anya@gmail.com
// pass 233dE#sdf

export function RegisterForm(): JSX.Element {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [registerError, setRegisterError] = useState<null | string>(null);
    const [registerSuccess, setRegisterSuccess] = useState<boolean>(false);

    const { register, handleSubmit, setError, formState: { errors } } = useForm<RegisterFormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onSubmit = async (data: RegisterFormData) => {
        setIsLoading(true);

        const response = await AuthApi.register(data);
        if (response.ok) {
            setRegisterSuccess(true);
        } else {
            const result: { resultCode: number, message: string } = await response.json();
            setRegisterError(result.message);
            setError('email', {});
            setError('login', {});
            setError('password', {});
            setError('confirm_password', {});
        }

        setIsLoading(false)
    }

    return (
        <div className={css.container}>
            {isLoading && <SpinerOverlay size={1.5} />}
            <Box padding={4}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <h2>Registration</h2>
                    {registerError &&
                        <h5 className='mt-3 text-color-error'>
                            {registerError}
                        </h5>
                    }

                    {!registerSuccess
                        ? <>
                            <div className="mt-3">
                                <Input
                                    type='text'
                                    placeholder='Email'
                                    {...register('email')}
                                    errorState={errors.email ? true : false}
                                />
                                {errors.email &&
                                    <div className="mt-2 text-size-2 text-color-error">
                                        {errors.email.message}
                                    </div>
                                }
                            </div>
                            <div className="mt-3">
                                <Input
                                    type="text"
                                    placeholder="Login"
                                    {...register('login')}
                                    errorState={errors.login ? true : false}
                                />
                                {errors.login &&
                                    <div className="mt-2 text-size-2 text-color-error">
                                        {errors.login.message}
                                    </div>
                                }
                            </div>
                            <div className="mt-3">
                                <InputPassword
                                    placeholder='Password'
                                    {...register('password')}
                                    errorState={errors.password ? true : false}
                                />
                                {errors.password &&
                                    <div className="mt-2 text-size-2 text-color-error">
                                        {errors.password.message}
                                    </div>
                                }
                            </div>
                            <div className="mt-3">
                                <InputPassword
                                    placeholder="Confirm password"
                                    {...register('confirm_password')}
                                    errorState={errors.confirm_password ? true : false}
                                />
                                {errors.confirm_password &&
                                    <div className="mt-2 text-size-2 text-color-error">
                                        {errors.confirm_password.message}
                                    </div>
                                }
                            </div>
                            <div className="mt-3 d-flex gap-3 align-items-center justify-content-between">
                                <Button>Registration</Button>
                                <Link to='/login'>Log in</Link>
                            </div>
                        </>
                        : <>
                            <h3 className='text-color-success mt-3'>
                                Registration completed successfully.
                            </h3>
                            <div className="mt-3">
                                <Button onClick={() => navigate('/login')}>Login</Button>
                            </div>
                        </>
                    }
                </form>
            </Box>
        </div>
    );
}