import React, { JSX, useState } from 'react';
import css from './CardePreviewActions.module.scss';
import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit';
import { PatchCollection } from '@reduxjs/toolkit/dist/query/core/buildThunks';

import { ICardPreview, useDeleteCardMutation, useUpdateCardMutation } from '5_entities/cards';
import { deleteCardFromColumn, updateCardInColumn } from '5_entities/columns';
import { Button } from '6_shared/ui/Button/Button';
import { ReactComponent as SvgIconDelete } from '6_shared/assets/icons/delete.svg';
import { ReactComponent as SvgIconEdit } from '6_shared/assets/icons/pencil.svg';
import { PopOver } from '6_shared/ui/PopOver/PopOver';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';
import { TextareaAutoHeight } from '6_shared/ui/fields/TextareaAutoHeight';

interface CardePreviewActionsProps {
    boardId: number;
    columnId: number;
    cardData: ICardPreview;
}

export function CardePreviewActions({ boardId, columnId, cardData }: CardePreviewActionsProps): JSX.Element {
    const [editMode, setEditMode] = useState(false);
    const [textareaValue, setTextareaValue] = useState(cardData.title);

    const [updateCard] = useUpdateCardMutation();
    const [deleteCard, { isLoading: isCardDeleting }] = useDeleteCardMutation();

    const firstLine = cardData.title.slice(0, 25);
    const remainingText = cardData.title.slice(25);

    const onBeforeQueryFulfilled = (dispatch: ThunkDispatch<any, any, AnyAction>): PatchCollection => {
        return dispatch(
            updateCardInColumn(boardId, cardData.columnId, cardData.cardId, { title: textareaValue })
        )
    }

    const onBlurHandler = () => {
        setEditMode(false);
        if (textareaValue === cardData.title || textareaValue.trim().length === 0) {
            setTextareaValue(cardData.title);
        } else {
            updateCard({
                boardId,
                columnId: cardData.columnId,
                cardId: cardData.cardId,
                body: { title: textareaValue },
                onBeforeQueryFulfilled
            });
        }
    }

    const onDeleteCardHandler = () => {
        deleteCard({
            cardId: cardData.cardId,
            onQueryFulfilled: (dispatch) => {
                dispatch(
                    deleteCardFromColumn(boardId, columnId, cardData.cardId)
                )
            }
        })
    }



    return (
        <div className={css.CardePreviewActions} data-id={cardData.cardId}>
            {!editMode
                ? <>
                    <div className={css.CardePreviewActions__buttons} onClick={(e) => e.stopPropagation()}>
                        <div className={css.CardePreviewActions__btn}>
                            <Button size={0.7} onClick={() => setEditMode(true)} variant='transparent'>
                                <SvgIconEdit />
                            </Button>
                        </div>
                        <div className={css.CardePreviewActions__btn}>
                            <PopOver
                                mode='inline'
                                title={<h4>Deleting a card</h4>}
                                content={
                                    <>
                                        {isCardDeleting && <SpinerOverlayAbsolute size={1} />}
                                        <p>Are you sure? The card will be deleted permanently!</p>
                                        <Button
                                            variant='danger'
                                            onClick={onDeleteCardHandler}
                                            style={{ width: '100%' }}
                                        >
                                            Delete Card
                                        </Button>
                                    </>
                                }
                            >
                                <Button size={0.7} variant='transparent'>
                                    <SvgIconDelete />
                                    
                                </Button>
                            </PopOver>
                        </div>
                    </div>
                    <div className={css.CardePreviewActions__body}>
                        <p>{firstLine}</p>
                        {!!remainingText && <p>{remainingText}</p>}
                    </div>
                </>
                : <TextareaAutoHeight
                    className={css.CardePreviewActions__textarea}
                    autoFocus
                    value={textareaValue}
                    setValue={setTextareaValue}
                    onBlur={onBlurHandler}
                    onKeyDown={(e) => {
                        if (e.key === 'Enter') onBlurHandler();
                    }}
                    onFocus={(e) => e.target.selectionStart = e.target.value.length}
                    onClick={(e) => e.stopPropagation()}
                    rows={1}
                >{cardData.title}</TextareaAutoHeight>
            }

        </div>
    );
}


