import React, { JSX, useState } from 'react';
import css from './CreateCardButton.module.scss';
import cn from 'classnames';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import { ICardPreview, useCreateCardMutation } from '5_entities/cards';
import { addCardToColumn } from '5_entities/columns';
import { Button } from '6_shared/ui/Button/Button';
import { ReactComponent as SvgIconCLose } from '6_shared/assets/icons/close.svg';
import { ReactComponent as SvgIconPlus } from '6_shared/assets/icons/plus.svg';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';
import { useAppDispatch } from '6_shared/store/hooks';

interface CreateCardButtonProps {
    boardId: number;
    columnId: number;
}

interface FormData {
    title: string;
}

const schema = yup.object().shape({
    title:
        yup.string()
            .min(2, "Title of card must have at least 2 characters")
            .required('Write the card title'),
}).required();

export function CreateCardButton({ boardId, columnId }: CreateCardButtonProps): JSX.Element {
    const dispatch = useAppDispatch();
    const [createCard, { isLoading }] = useCreateCardMutation();
    const [editMode, setEditMode] = useState(false);

    const { register, handleSubmit, setValue, formState: { errors } } = useForm<FormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onQueryFulfilled = (boardId: number, columnId: number, cardData: ICardPreview) => {
        dispatch(
            addCardToColumn(boardId, columnId, cardData)
        );
    }

    const onCreateCard = async (data: FormData) => {
        try {
            await createCard({ boardId, columnId, title: data.title, onQueryFulfilled }).unwrap();
            setValue('title', '');
            setEditMode(false);
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <div className={css.CreateCardButton}>
            {isLoading && <SpinerOverlayAbsolute size={1} />}
            {!editMode
                ? <Button
                    variant='transparent'
                    style={{ width: '100%', justifyContent: 'start' }}
                    onClick={() => setEditMode(true)}
                >
                    <SvgIconPlus />
                    Create Card
                </Button>
                : <form onSubmit={handleSubmit(onCreateCard)}>
                    <textarea
                        className={cn(
                            css.CreateCardButton__textarea,
                            { [css.error]: errors.title ? true : false }
                        )}
                        {...register('title')}
                        autoFocus
                    ></textarea>
                    {errors.title &&
                        <div className="mt-2 text-size-2 text-color-error text-start">
                            {errors.title.message}
                        </div>
                    }
                    <div className="d-flex gap-2 align-items-center mt-2">
                        <Button type='submit'>Create card</Button>
                        <Button
                            onClick={(e) => {
                                e.stopPropagation();
                                setEditMode(false);
                            }}
                            type='button'
                            variant='transparent'
                        >
                            <SvgIconCLose />
                        </Button>
                    </div>
                </form>
            }
        </div>
    );
}