import React, { JSX, useState } from 'react';
import css from './CardImages.module.scss';
import { useForm } from 'react-hook-form';
import { createPortal } from 'react-dom';
import ImageViewer from 'react-simple-image-viewer';

import { ICardImage, useDeletaImageMutation, useUploadImagesMutation } from '5_entities/cards';
import { addDomainNameToPath } from '5_entities/utils/addDomainNameToPath';
import { Button } from '6_shared/ui/Button/Button';
import { useAppDropzone } from '6_shared/ui/Dropzone/useDropzone';
import { SpinerOverlayAbsolute } from '6_shared/ui/Spiner/Spiner';
import { ServerImg } from '6_shared/ui/ServerImg/ServerImg';
import { ReactComponent as SvgIconDelet } from '6_shared/assets/icons/delete.svg';

interface CardImagesProps {
    cardId: number;
    images: ICardImage[],
}

interface FormData {
    images?: File[];
}

export function CardImages({ cardId, images }: CardImagesProps): JSX.Element {
    const [uploadImages, { isLoading }] = useUploadImagesMutation();
    const { DropzoneContainer, resetDropzone } = useAppDropzone();

    const { handleSubmit, formState: { errors, isDirty }, control, resetField } = useForm<FormData>({
        mode: 'onSubmit',
        defaultValues: {
            images: undefined
        },
    });

    const onSubmit = async (data: FormData) => {
        if (!data.images) return;

        try {
            await uploadImages({ cardId, images: data.images }).unwrap();
            resetForm();
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <>
            {isLoading && <SpinerOverlayAbsolute size={1.5} />}
            <div className={css.CardImages}>
                {!!images.length
                    && <div className={css.CardImages__list}>
                        {images.map(image =>
                            <Image key={image.id} imageData={image} />
                        )}
                    </div>
                }
                <form onSubmit={handleSubmit(onSubmit)} className={css.CardImages__form}>
                    <DropzoneContainer control={control} name={'images'} multiple={true} />
                    <div className="mt-2">
                        <Button disabled={!isDirty}>Upload</Button>
                    </div>
                </form>
            </div>
        </>
    );

    function resetForm() {
        resetField('images');
        resetDropzone();
    }
}

interface ImageProps {
    imageData: ICardImage;
}

function Image({ imageData }: ImageProps): JSX.Element {
    const [isViewerOpen, setIsViewerOpen] = useState<boolean>(false);
    const [deleteImage, { isLoading }] = useDeletaImageMutation();

    const onDeleteImageHandler = () => {
        deleteImage({ cardId: imageData.cardId, imageId: imageData.id })
    }

    return (
        <>
            <div className={css.image}>
                {isLoading && <SpinerOverlayAbsolute size={1} />}

                <div
                    className={css.image__container}
                    onClick={() => setIsViewerOpen(true)}
                >
                    <ServerImg src={imageData.url} />
                </div>
                <div className={css.image__info}>
                    <div className={css.image__name}>{imageData.imageName}</div>
                    <div className={css.image__btn}>
                        <Button variant='transparent' onClick={onDeleteImageHandler}>
                            <SvgIconDelet />  Delete image
                        </Button>
                    </div>
                </div>
            </div>
            {createPortal(
                <>
                    {isViewerOpen &&
                        <ImageViewer
                            src={[addDomainNameToPath(imageData.url)]}
                            backgroundStyle={{ zIndex: 10 }}
                            onClose={() => setIsViewerOpen(false)}
                        />
                    }
                </>,
                document.body
            )}
        </>
    );
}