import React, { JSX, useState } from 'react';
import { Link } from 'react-router-dom';

import css from './Auth.module.scss';

import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import { IMe, LoginFormData } from '../types/types';
import { AuthApi } from '../api/AuthApi';
import { setAuth, setMe } from '../store/authSlice';

import { Box } from '6_shared/ui/Box/Box';
import { Input } from '6_shared/ui/fields/Input';
import { InputPassword } from '6_shared/ui/fields/InputPassword';
import { Button } from '6_shared/ui/Button/Button';
import { useAppDispatch } from '6_shared/store/hooks';
import { SpinerOverlay } from '6_shared/ui/Spiner/Spiner';


const schema = yup.object().shape({
    email:
        yup.string()
            .email('Email must be a valid email')
            .required('Email is a required field'),
    password:
        yup.string()
            .min(4, "Password must have at least 8 characters")
            .required('Password is a required field'),
}).required();

// petya@gmail.com
// anya@gmail.com
// vitalii@gmail.com
// pass 233dE#sdf

export function LoginForm(): JSX.Element {
    const dispatch = useAppDispatch();
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [isError, setIsError] = useState<boolean>(false);

    const { register, handleSubmit, setError, formState: { errors } } = useForm<LoginFormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    const onSubmit = async (data: LoginFormData) => {
        setIsLoading(true);

        const response = await AuthApi.login(data);
        if (response.ok) {
            const jwt = response.headers.get('X-JWT');
            if (jwt) {
                localStorage.setItem('jwt', jwt);
                const meResponse = await AuthApi.me(jwt);
                if (meResponse.ok) {
                    const me: { resultCode: number, data: IMe } = await meResponse.json();
                    dispatch(setMe(me.data));
                    dispatch(setAuth(true));
                }
            }
            dispatch(setAuth(true));
        } else {
            setIsError(true);
            setError('email', {});
            setError('password', {});
        }

        setIsLoading(false)
    }

    return (
        <div className={css.container}>
            {isLoading && <SpinerOverlay size={1.5} />}
            <Box padding={4}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <h2>Log in</h2>
                    {isError &&
                        <h5 className='mt-3 text-color-error'>Email or Password is wrong!</h5>
                    }
                    <div className="mt-3">
                        <Input
                            type='text'
                            placeholder='Email'
                            {...register('email')}
                            errorState={errors.email ? true : false}
                        />
                        {errors.email &&
                            <div className="mt-2 text-size-2 text-color-error">
                                {errors.email.message}
                            </div>
                        }
                    </div>
                    <div className="mt-3">
                        <InputPassword
                            placeholder='Password'
                            {...register('password')}
                            errorState={errors.password ? true : false}
                        />
                        {errors.password &&
                            <div className="mt-2 text-size-2 text-color-error">
                                {errors.password.message}
                            </div>
                        }
                    </div>
                    <div className="mt-3 d-flex align-items-center justify-content-between gap-3">
                        <Button>Login</Button>
                        <Link to='/register'>Registration</Link>
                    </div>
                </form>
            </Box>
        </div>
    );
}